interface BackendResponse {
  success: boolean;
  words: string[];
  fallback: boolean;
  error?: string;
  regularWord?: string;
}

class PerplexityService {
  private static readonly VERCEL_API_URL = 'https://react-component-portfolio.vercel.app/api/generate-undercover-words';

  /**
   * Generate undercover words using Vercel API for all environments
   */
  static async generateUndercoverWords(regularWord: string): Promise<string[]> {
    try {
      const response = await fetch(this.VERCEL_API_URL, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          regularWord: regularWord
        })
      });

      if (!response.ok) {
        throw new Error(`API request failed: ${response.status}`);
      }

      const data: BackendResponse = await response.json();
      
      if (data.fallback) {
        console.warn(` Using fallback words for "${regularWord}":`, data.words);
      }
      
      return data.words;
    } catch (error) {
      console.error(' Failed to generate undercover words via Vercel API:', error);
      
      // If Vercel API fails, throw error to let the calling code handle it
      const errorMessage = error instanceof Error ? error.message : String(error);
      throw new Error(`Unable to generate words for "${regularWord}": ${errorMessage}`);
    }
  }

  /**
   * Check API health by testing the Vercel endpoint
   */
  static async checkApiHealth(): Promise<boolean> {
    try {
      const response = await fetch('https://react-component-portfolio.vercel.app/api/health');
      return response.ok;
    } catch (error) {
      console.error(' Vercel API health check failed:', error);
      return false;
    }
  }
}

export default PerplexityService;
