import React from 'react';
import { useSortable } from '@dnd-kit/sortable';
import { GripVertical } from 'lucide-react';
import { TabData } from './tabsData';

interface SortableTabProps {
  tab: TabData;
  isActive: boolean;
  isPinned: boolean;
  onClick: () => void;
  onPin: () => void;
}

export const SortableTab: React.FC<SortableTabProps> = ({
  tab,
  isActive,
  isPinned,
  onClick,
  onPin,
}) => {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({ 
    id: tab.id,
    disabled: isPinned // Disable dragging for pinned tabs
  });

  // Constrain transform to only X-axis (horizontal movement only)
  const style = {
    transform: transform ? `translate3d(${transform.x}px, 0, 0)` : undefined,
    transition,
  };

  const IconComponent = tab.icon;

  const handlePinClick = (e: React.MouseEvent) => {
    e.stopPropagation();
    onPin();
  };

  return (
    <div
      ref={setNodeRef}
      style={style}
      className={`sortable-tab ${isActive ? 'sortable-tab--active' : ''} ${isDragging ? 'sortable-tab--dragging' : ''} ${isPinned ? 'sortable-tab--pinned' : ''}`}
      onClick={onClick}
      data-tab-color={tab.color}
      data-tab-bg={tab.bgColor}
    >
      <div className="sortable-tab__content">
        {!isPinned && (
          <div
            className="sortable-tab__drag-handle"
            {...attributes}
            {...listeners}
          >
            <GripVertical size={14} />
          </div>
        )}
        
        <div className="sortable-tab__icon">
          <IconComponent size={16} />
        </div>
        
        <span className="sortable-tab__title">{tab.title}</span>
        
        <div className="sortable-tab__actions">
          <button
            className={`sortable-tab__pin ${isPinned ? 'sortable-tab__pin--active' : ''}`}
            onClick={handlePinClick}
            aria-label={`${isPinned ? 'Unpin' : 'Pin'} ${tab.title} tab`}
            title={`${isPinned ? 'Unpin' : 'Pin'} tab`}
          >
            <svg width="14" height="14" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
              <path d="M12 17v5"/>
              <path d="M9 10.76a2 2 0 0 1-1.11 1.79l-1.78.9A2 2 0 0 0 7.89 17H16.1a2 2 0 0 0 1.78-2.55l-1.78-.9A2 2 0 0 1 15 10.76V7a1 1 0 0 1 1-1 2 2 0 0 0 0-4H8a2 2 0 0 0 0 4 1 1 0 0 1 1 1z"/>
              {isPinned && (
                <line x1="0" y1="0" x2="22" y2="22" strokeWidth="2" stroke="currentColor"/>
              )}
            </svg>
          </button>
        </div>
      </div>
    </div>
  );
};
