const getRandomDate = () => {
  const now = new Date();
  const daysAgo = Math.floor(Math.random() * 365);
  const randomDate = new Date(now.getTime() - daysAgo * 24 * 60 * 60 * 1000);
  return randomDate.toLocaleDateString('en-US', {
    year: 'numeric',
    month: 'short',
    day: 'numeric'
  });
};

const getRandomElement = <T>(array: T[]): T => {
  return array[Math.floor(Math.random() * array.length)];
};

const generateUser = (index: number): TableRow => {
  const firstName = getRandomElement(firstNames);
  const lastName = getRandomElement(lastNames);
  const name = `${firstName} ${lastName}`;
  const email = `${firstName.toLowerCase()}.${lastName.toLowerCase()}@${getRandomElement(domains)}`;
  
  return {
    id: `USR${String(index + 1000).padStart(4, '0')}`,
    name,
    email,
    role: getRandomElement(roles),
    status: getRandomElement(statuses),
    lastLogin: getRandomDate(),
    score: Math.floor(Math.random() * 100) + 1
  };
};

// Generate 150 sample records
export const tableData: TableRow[] = Array.from({ length: 150 }, (_, index) => 
  generateUser(index)
);

