import React from 'react';
import { PlayerAssignment } from './AssignmentPhase';
import './ResultsPhase.scss';

interface ResultsPhaseProps {
  winner: 'civilians' | 'undercovers';
  playerAssignments: PlayerAssignment[];
  onPlayAgain: () => void;
}

const getRoleIcon = (role: string): string => {
  switch (role) {
    case 'regular': return '';
    case 'undercover': return '';
    case 'mrwhite': return '';
    default: return '';
  }
};

const getRoleDisplay = (role: string): string => {
  switch (role) {
    case 'regular': return 'Civilian';
    case 'undercover': return 'Undercover';
    case 'mrwhite': return 'Mr. White';
    default: return 'Civilian';
  }
};

export const ResultsPhase: React.FC<ResultsPhaseProps> = ({
  winner,
  playerAssignments,
  onPlayAgain
}) => {
  // Get the words used in the game
  const civilianWord = playerAssignments.find(p => p.role === 'regular')?.word;
  const undercoverWord = playerAssignments.find(p => p.role === 'undercover')?.word;

  // Group players by role for better display
  const civilians = playerAssignments.filter(p => p.role === 'regular');
  const undercovers = playerAssignments.filter(p => p.role === 'undercover');
  const mrWhites = playerAssignments.filter(p => p.role === 'mrwhite');

  return (
    <div className="results-phase">
      <div className="results-phase__header">
        <div className={`winner-announcement winner-announcement--${winner}`}>
          <div className="winner-icon">
            {winner === 'civilians' ? '' : ''}
          </div>
          <h1 className="winner-title">
            {winner === 'civilians' ? 'Civilians Win!' : 'Undercovers Win!'}
          </h1>
          <p className="winner-subtitle">
            {winner === 'civilians' 
              ? 'The truth prevails! All threats have been eliminated.' 
              : 'Deception triumphs! The undercovers have succeeded.'}
          </p>
        </div>
      </div>

      <div className="results-phase__content">
        <div className="game-words">
          <h2>Game Words</h2>
          <div className="words-display">
            <div className="word-card word-card--civilian">
              <div className="word-label"> Civilian Word</div>
              <div className="word-value">{civilianWord}</div>
            </div>
            {undercoverWord && undercoverWord !== civilianWord && (
              <div className="word-card word-card--undercover">
                <div className="word-label"> Undercover Word</div>
                <div className="word-value">{undercoverWord}</div>
              </div>
            )}
          </div>
        </div>

        <div className="player-roles">
          <h2>Player Roles Revealed</h2>
          
          {civilians.length > 0 && (
            <div className="role-group">
              <h3 className="role-group__title">
                <span className="role-icon"></span>
                Civilians ({civilians.length})
              </h3>
              <div className="role-group__players">
                {civilians.map((player) => (
                  <div key={player.name} className={`player-card ${!player.isAlive ? 'player-card--eliminated' : ''}`}>
                    <span className="player-name">{player.name}</span>
                    <span className="player-word">"{player.word}"</span>
                    {!player.isAlive && <span className="elimination-badge">Eliminated</span>}
                  </div>
                ))}
              </div>
            </div>
          )}

          {undercovers.length > 0 && (
            <div className="role-group">
              <h3 className="role-group__title">
                <span className="role-icon"></span>
                Undercovers ({undercovers.length})
              </h3>
              <div className="role-group__players">
                {undercovers.map((player) => (
                  <div key={player.name} className={`player-card ${!player.isAlive ? 'player-card--eliminated' : ''}`}>
                    <span className="player-name">{player.name}</span>
                    <span className="player-word">"{player.word}"</span>
                    {!player.isAlive && <span className="elimination-badge">Eliminated</span>}
                  </div>
                ))}
              </div>
            </div>
          )}

          {mrWhites.length > 0 && (
            <div className="role-group">
              <h3 className="role-group__title">
                <span className="role-icon"></span>
                Mr. White ({mrWhites.length})
              </h3>
              <div className="role-group__players">
                {mrWhites.map((player) => (
                  <div key={player.name} className={`player-card ${!player.isAlive ? 'player-card--eliminated' : ''}`}>
                    <span className="player-name">{player.name}</span>
                    <span className="player-word">No word</span>
                    {!player.isAlive && <span className="elimination-badge">Eliminated</span>}
                  </div>
                ))}
              </div>
            </div>
          )}
        </div>
      </div>

      <div className="results-phase__actions">
        <button className="play-again-btn" onClick={onPlayAgain}>
           Play Again
        </button>
      </div>
    </div>
  );
};
