import React, { useState, useRef, useEffect } from 'react';
import { Edit3, X, Check } from 'lucide-react';
import './Note.scss';

interface NoteProps {
  id: string;
  x: number;
  y: number;
  content: string;
  onUpdate: (id: string, updates: { x?: number; y?: number; content?: string }) => void;
  onRemove: (id: string) => void;
}

export const Note: React.FC<NoteProps> = ({
  id,
  x,
  y,
  content,
  onUpdate,
  onRemove,
}) => {
  const [isEditing, setIsEditing] = useState(false);
  const [editContent, setEditContent] = useState(content);
  // Dragging is now handled by SortableNote wrapper
  
  const textareaRef = useRef<HTMLTextAreaElement>(null);
  const noteRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (isEditing && textareaRef.current) {
      textareaRef.current.focus();
      textareaRef.current.select();
    }
  }, [isEditing]);

  // Mouse down handling removed - now handled by SortableNote

  const handleEdit = () => {
    setIsEditing(true);
    setEditContent(content);
  };

  const handleSave = () => {
    onUpdate(id, { content: editContent.trim() });
    setIsEditing(false);
  };

  const handleCancel = () => {
    setEditContent(content);
    setIsEditing(false);
  };

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && e.ctrlKey) {
      handleSave();
    } else if (e.key === 'Escape') {
      handleCancel();
    }
  };

  const handleRemove = (e: React.MouseEvent) => {
    e.stopPropagation();
    onRemove(id);
  };

  return (
    <div
      ref={noteRef}
      className={`note ${isEditing ? 'note--editing' : ''}`}
      style={{
        position: 'relative',
        cursor: isEditing ? 'text' : 'grab',
      }}
    >
      <div className="note__header">
        <div className="note__actions">
          {!isEditing ? (
            <>
              <button
                className="note__action-btn"
                onClick={handleEdit}
                title="Edit note"
              >
                <Edit3 size={12} />
              </button>
              <button
                className="note__action-btn note__action-btn--remove"
                onClick={handleRemove}
                title="Remove note"
              >
                <X size={12} />
              </button>
            </>
          ) : (
            <>
              <button
                className="note__action-btn note__action-btn--save"
                onClick={handleSave}
                title="Save (Ctrl+Enter)"
              >
                <Check size={12} />
              </button>
              <button
                className="note__action-btn note__action-btn--cancel"
                onClick={handleCancel}
                title="Cancel (Esc)"
              >
                <X size={12} />
              </button>
            </>
          )}
        </div>
      </div>
      
      <div className="note__content">
        {isEditing ? (
          <textarea
            ref={textareaRef}
            value={editContent}
            onChange={(e) => setEditContent(e.target.value)}
            onKeyDown={handleKeyDown}
            className="note__textarea"
            placeholder="Enter your note..."
            rows={3}
            onMouseDown={(e) => e.stopPropagation()}
          />
        ) : (
          <div className="note__text">
            {content || 'Empty note'}
          </div>
        )}
      </div>
      
      {isEditing && (
        <div className="note__hint">
          Ctrl+Enter to save, Esc to cancel
        </div>
      )}
    </div>
  );
};
