import React, { useMemo } from 'react';
import { useSortable } from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';
import { Note } from './Note';

interface SortableNoteProps {
  note: {
    id: string;
    x: number;
    y: number;
    content: string;
  };
  onUpdate: (id: string, updates: { x?: number; y?: number; content?: string }) => void;
  onRemove: (id: string) => void;
}

export const SortableNote: React.FC<SortableNoteProps> = ({
  note,
  onUpdate,
  onRemove,
}) => {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({
    id: note.id,
    data: {
      type: 'note',
      note,
    },
  });

  const style = useMemo(() => ({
    position: 'absolute' as const,
    left: note.x,
    top: note.y,
    transform: CSS.Transform.toString(transform),
    transition,
    zIndex: isDragging ? 1000 : 1,
  }), [note.x, note.y, transform, transition, isDragging]);

  return (
    <div
      ref={setNodeRef}
      style={style}
      {...attributes}
      {...listeners}
      data-note-id={note.id}
    >
      <Note
        id={note.id}
        x={0} // Position is handled by the sortable wrapper
        y={0} // Position is handled by the sortable wrapper
        content={note.content}
        onUpdate={onUpdate}
        onRemove={onRemove}
      />
    </div>
  );
};
