import React, {useEffect, useMemo, useState} from 'react';
import { useSortable } from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';
import { ConnectorItem } from './ConnectorItem';
import { ConnectorType } from './connectorTypes';

interface CanvasConnector extends ConnectorType {
  id: string;
  x: number;
  y: number;
}

interface SortableConnectorProps {
  connector: CanvasConnector;
  onRemove: (id: string) => void;
  onStartConnection: (fromId: string) => void;
  onFinishConnection: (toId: string) => void;
  onConfigureConnector: (connector: CanvasConnector) => void;
  isConnecting: boolean;
  connectingFrom: string | null;
}

export const SortableConnector: React.FC<SortableConnectorProps> = ({
  connector,
  onRemove,
  onStartConnection,
  onFinishConnection,
  onConfigureConnector,
  isConnecting,
  connectingFrom,
}) => {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({
    id: connector.id,
  });

  const style = useMemo(() => ({
    transform: CSS.Transform.toString(transform),
    transition,
    position: 'absolute' as const,
    left: connector.x,
    top: connector.y,
    zIndex: isDragging ? 1000 : 1,
  }), [connector, transform, transition, isDragging]);

  const [, setForceUpdate] = useState(0);

  const handleRemove = () => {
    onRemove(connector.id);
  };

  const handleStartConnection = () => {
    onStartConnection(connector.id);
  };

  const handleFinishConnection = () => {
    onFinishConnection(connector.id);
  };

  useEffect(() => {
    setForceUpdate(prev => prev + 1);
  }, [connector]);

  const isConnectionSource = connectingFrom === connector.id;
  const canBeTarget = isConnecting && !isConnectionSource;

  const handleConnectorClick = () => {
    // Open configuration panel when not dragging and not in connecting mode
    if (!isConnecting && !isDragging) {
      onConfigureConnector(connector);
    }
  };

  return (
    <div
      ref={setNodeRef}
      style={{
        ...style,
        position: 'absolute',
      }}
      {...attributes}
      {...listeners}
      className={`sortable-connector ${canBeTarget ? 'sortable-connector--target' : ''} ${isConnectionSource ? 'sortable-connector--source' : ''}`}
      onClick={canBeTarget ? handleFinishConnection : handleConnectorClick}
      data-connector-id={connector.id}
    >
      <ConnectorItem
        connector={connector}
        isDragging={isDragging}
        onStartConnection={handleStartConnection}
        showConnectionPoint={true}
        isConnecting={isConnecting}
        isConnectionSource={isConnectionSource}
        canBeTarget={canBeTarget}
        isCompact={true}
        onClick={handleConnectorClick}
      />

      {/* Remove button positioned absolutely in top right corner */}
      <button
        className="sortable-connector__remove"
        onClick={(e) => {
          e.stopPropagation();
          handleRemove();
        }}
        aria-label="Remove connector"
        title="Remove connector"
      >
        <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
          <line x1="18" y1="6" x2="6" y2="18"></line>
          <line x1="6" y1="6" x2="18" y2="18"></line>
        </svg>
      </button>
    </div>
  );
};
