import React, { useState } from 'react';
import { useDraggable } from '@dnd-kit/core';
import { ChevronDown, ChevronUp } from 'lucide-react';
import { ConnectorItem } from './ConnectorItem';
import { connectorTypes } from './connectorTypes';
import './ConnectorPalette.scss';

const DraggableConnector: React.FC<{ connector: any; isMinified?: boolean }> = ({ connector, isMinified = false }) => {
  const nodeRef = React.useRef<HTMLDivElement>(null);
  
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    isDragging,
  } = useDraggable({
    id: connector.id,
    data: {
      type: 'palette-connector',
      connector,
      getNode: () => nodeRef.current,
      nodeRef: nodeRef,
    },
  });
  
  const combinedRef = React.useCallback((node: HTMLDivElement) => {
    nodeRef.current = node;
    setNodeRef(node);
  }, [setNodeRef]);

  const style = transform ? {
    transform: `none`,
  } : undefined;

  return (
    <div
      ref={combinedRef}
      style={style}
      {...listeners}
      {...attributes}
      className={`connector-palette__item ${isDragging ? 'dragging' : ''} ${isMinified ? 'connector-palette__item--minified' : ''}`}
    >
      <ConnectorItem
        connector={connector}
        isDragging={isDragging}
        isCompact={isMinified}
      />
    </div>
  );
};

const DraggableConnectorWithState: React.FC<{ connector: any; isMinified: boolean }> = ({ connector, isMinified }) => {
  return <DraggableConnector connector={connector} isMinified={isMinified} />;
};

export const ConnectorPalette: React.FC = () => {
  const [isCollapsed, setIsCollapsed] = useState(false);
  const categories = Array.from(new Set(connectorTypes.map(c => c.category)));

  const toggleCollapse = () => {
    setIsCollapsed(!isCollapsed);
  };

  return (
    <div className={`connector-palette ${isCollapsed ? 'connector-palette--collapsed' : ''}`}>
      <div className="connector-palette__header">
        <div className="connector-palette__header-content">
          <h4>Connector Palette</h4>
          {!isCollapsed && <p>Drag connectors to the canvas</p>}
        </div>
        <button 
          className="connector-palette__toggle"
          onClick={toggleCollapse}
          title={isCollapsed ? 'Expand palette' : 'Collapse palette'}
        >
          {isCollapsed ? <ChevronDown size={20} /> : <ChevronUp size={20} />}
        </button>
      </div>
      
      <div className="connector-palette__content">
        {categories.map(category => (
          <div key={category} className="connector-palette__category">
            <h5 className="connector-palette__category-title">{category}</h5>
            <div className="connector-palette__category-items">
              {connectorTypes
                .filter(connector => connector.category === category)
                .map(connector => (
                  <DraggableConnectorWithState 
                    key={connector.id} 
                    connector={connector}
                    isMinified={isCollapsed}
                  />
                ))}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};
