import React, { useState } from 'react';
import { SetupPhase } from './phases/SetupPhase';
import { AssignmentPhase, PlayerAssignment } from './phases/AssignmentPhase';
import { GameplayPhase } from './phases/GameplayPhase';
import { ResultsPhase } from './phases/ResultsPhase';
import './UndercoverGame.scss';

export type GamePhase = 'setup' | 'assignment' | 'gameplay' | 'results';

export interface GameConfig {
  numPlayers: number;
  numUndercovers: number;
  numMrWhites: number;
  playerNames: string[];
  selectedCategories: {
    objects: boolean;
    foods: boolean;
    animals: boolean;
    abstracts: boolean;
    places: boolean;
  };
}

export const UndercoverGame: React.FC = () => {
  const [currentPhase, setCurrentPhase] = useState<GamePhase>('setup');
  const [playerAssignments, setPlayerAssignments] = useState<PlayerAssignment[]>([]);
  const [winner, setWinner] = useState<'civilians' | 'undercovers' | null>(null);
  const [gameConfig, setGameConfig] = useState<GameConfig>({
    numPlayers: 5,
    numUndercovers: 1,
    numMrWhites: 1,
    playerNames: [],
    selectedCategories: {
      objects: true,
      foods: true,
      animals: true,
      abstracts: true,
      places: true
    }
  });

  const handleStartGame = (config: GameConfig) => {
    setGameConfig(config);
    setCurrentPhase('assignment');
  };

  const handleAssignmentComplete = (assignments: PlayerAssignment[]) => {
    setPlayerAssignments(assignments);
    setCurrentPhase('gameplay');
  };

  const handleGameComplete = (gameWinner: 'civilians' | 'undercovers') => {
    setWinner(gameWinner);
    setCurrentPhase('results');
  };

  const handlePlayAgain = () => {
    setCurrentPhase('setup');
    setPlayerAssignments([]);
    setWinner(null);
    setGameConfig({
      numPlayers: 5,
      numUndercovers: 1,
      numMrWhites: 1,
      playerNames: [],
      selectedCategories: {
        objects: true,
        foods: true,
        animals: true,
        abstracts: true,
        places: true
      }
    });
  };

  const renderPhase = () => {
    switch (currentPhase) {
      case 'setup':
        return <SetupPhase onStartGame={handleStartGame} />;
      case 'assignment':
        return <AssignmentPhase gameConfig={gameConfig} onPhaseComplete={handleAssignmentComplete} />;
      case 'gameplay':
        return (
          <GameplayPhase
            gameConfig={gameConfig}
            playerAssignments={playerAssignments}
            onGameComplete={handleGameComplete}
          />
        );
      case 'results':
        return winner ? (
          <ResultsPhase 
            winner={winner}
            playerAssignments={playerAssignments}
            onPlayAgain={handlePlayAgain}
          />
        ) : (
          <div>Loading results...</div>
        );
      default:
        return <SetupPhase onStartGame={handleStartGame} />;
    }
  };

  return (
    <div className="undercover-game">
      <div className="undercover-game__phase-indicator">
        <div className="phase-steps">
          <div className={`phase-step ${currentPhase === 'setup' ? 'phase-step--active' : ''}`}>
            <span className="phase-step__number">1</span>
            <span className="phase-step__label">Setup</span>
          </div>
          <div className={`phase-step ${currentPhase === 'assignment' ? 'phase-step--active' : ''}`}>
            <span className="phase-step__number">2</span>
            <span className="phase-step__label">Assignment</span>
          </div>
          <div className={`phase-step ${currentPhase === 'gameplay' ? 'phase-step--active' : ''}`}>
            <span className="phase-step__number">3</span>
            <span className="phase-step__label">Gameplay</span>
          </div>
          <div className={`phase-step ${currentPhase === 'results' ? 'phase-step--active' : ''}`}>
            <span className="phase-step__number">4</span>
            <span className="phase-step__label">Results</span>
          </div>
        </div>
      </div>

      <div className="undercover-game__content">
        {renderPhase()}
      </div>
    </div>
  );
};
