import React, { useState, useMemo } from 'react';
import { 
  ChevronLeft, 
  ChevronRight, 
  ChevronsLeft, 
  ChevronsRight,
  Search,
  Filter,
  Download,
  RefreshCw,
  ArrowUpDown,
  ArrowUp,
  ArrowDown
} from 'lucide-react';
import { tableData, TableRow, SortDirection } from './tableData';
import './PaginatedTable.scss';

export const PaginatedTable: React.FC = () => {
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(10);
  const [searchTerm, setSearchTerm] = useState('');
  const [sortColumn, setSortColumn] = useState<keyof TableRow>('name');
  const [sortDirection, setSortDirection] = useState<SortDirection>('asc');
  const [selectedRows, setSelectedRows] = useState<Set<string>>(new Set());
  const [isLoading, setIsLoading] = useState(false);

  // Filter and sort data
  const filteredAndSortedData = useMemo(() => {
    let filtered = tableData.filter(row =>
      Object.values(row).some(value =>
        value.toString().toLowerCase().includes(searchTerm.toLowerCase())
      )
    );

    // Sort data
    filtered.sort((a, b) => {
      const aValue = a[sortColumn];
      const bValue = b[sortColumn];
      
      if (typeof aValue === 'string' && typeof bValue === 'string') {
        const comparison = aValue.localeCompare(bValue);
        return sortDirection === 'asc' ? comparison : -comparison;
      }
      
      if (typeof aValue === 'number' && typeof bValue === 'number') {
        return sortDirection === 'asc' ? aValue - bValue : bValue - aValue;
      }
      
      return 0;
    });

    return filtered;
  }, [searchTerm, sortColumn, sortDirection]);

  // Pagination calculations
  const totalPages = Math.ceil(filteredAndSortedData.length / itemsPerPage);
  const startIndex = (currentPage - 1) * itemsPerPage;
  const endIndex = startIndex + itemsPerPage;
  const currentData = filteredAndSortedData.slice(startIndex, endIndex);

  const handleSort = (column: keyof TableRow) => {
    if (sortColumn === column) {
      setSortDirection(sortDirection === 'asc' ? 'desc' : 'asc');
    } else {
      setSortColumn(column);
      setSortDirection('asc');
    }
  };

  const handleRowSelect = (id: string) => {
    const newSelected = new Set(selectedRows);
    if (newSelected.has(id)) {
      newSelected.delete(id);
    } else {
      newSelected.add(id);
    }
    setSelectedRows(newSelected);
  };

  const handleSelectAll = () => {
    if (selectedRows.size === currentData.length) {
      setSelectedRows(new Set());
    } else {
      setSelectedRows(new Set(currentData.map(row => row.id)));
    }
  };

  const handleRefresh = () => {
    setIsLoading(true);
    setTimeout(() => {
      setIsLoading(false);
    }, 1000);
  };

  const getSortIcon = (column: keyof TableRow) => {
    if (sortColumn !== column) {
      return <ArrowUpDown size={14} className="sort-icon sort-icon--inactive" />;
    }
    
    return sortDirection === 'asc' 
      ? <ArrowUp size={14} className="sort-icon sort-icon--active" />
      : <ArrowDown size={14} className="sort-icon sort-icon--active" />;
  };

  const getStatusColor = (status: string) => {
    switch (status.toLowerCase()) {
      case 'active':
        return 'status--success';
      case 'inactive':
        return 'status--error';
      case 'pending':
        return 'status--warning';
      default:
        return 'status--neutral';
    }
  };

  return (
    <div className="paginated-table">
      <div className="paginated-table__header">
        <div>
          <h3>Advanced Data Table</h3>
          <p>Sortable, filterable, and paginated table with bulk operations</p>
        </div>
        <div className="paginated-table__stats">
          <span className="paginated-table__count">
            {filteredAndSortedData.length} record{filteredAndSortedData.length !== 1 ? 's' : ''}
          </span>
          {selectedRows.size > 0 && (
            <span className="paginated-table__selected">
              {selectedRows.size} selected
            </span>
          )}
        </div>
      </div>

      <div className="paginated-table__container">
        {/* Table Controls */}
        <div className="table-controls">
          <div className="table-controls__left">
            <div className="search-input">
              <Search size={16} className="search-input__icon" />
              <input
                type="text"
                placeholder="Search records..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="search-input__field"
              />
            </div>
            <button className="btn-ghost table-controls__button">
              <Filter size={16} />
              Filter
            </button>
          </div>
          
          <div className="table-controls__right">
            <button 
              className="btn-ghost table-controls__button"
              onClick={handleRefresh}
              disabled={isLoading}
            >
              <RefreshCw size={16} className={isLoading ? 'spinning' : ''} />
              Refresh
            </button>
            <button className="btn-ghost table-controls__button">
              <Download size={16} />
              Export
            </button>
          </div>
        </div>

        {/* Table */}
        <div className="table-wrapper">
          <table className="data-table">
            <thead>
              <tr>
                <th className="data-table__header data-table__header--checkbox">
                  <input
                    type="checkbox"
                    checked={selectedRows.size === currentData.length && currentData.length > 0}
                    onChange={handleSelectAll}
                    className="checkbox"
                  />
                </th>
                <th 
                  className="data-table__header data-table__header--sortable"
                  onClick={() => handleSort('name')}
                >
                  Name
                  {getSortIcon('name')}
                </th>
                <th 
                  className="data-table__header data-table__header--sortable"
                  onClick={() => handleSort('email')}
                >
                  Email
                  {getSortIcon('email')}
                </th>
                <th 
                  className="data-table__header data-table__header--sortable"
                  onClick={() => handleSort('role')}
                >
                  Role
                  {getSortIcon('role')}
                </th>
                <th 
                  className="data-table__header data-table__header--sortable"
                  onClick={() => handleSort('status')}
                >
                  Status
                  {getSortIcon('status')}
                </th>
                <th 
                  className="data-table__header data-table__header--sortable"
                  onClick={() => handleSort('lastLogin')}
                >
                  Last Login
                  {getSortIcon('lastLogin')}
                </th>
                <th 
                  className="data-table__header data-table__header--sortable"
                  onClick={() => handleSort('score')}
                >
                  Score
                  {getSortIcon('score')}
                </th>
                <th className="data-table__header">Actions</th>
              </tr>
            </thead>
            <tbody>
              {currentData.map((row) => (
                <tr 
                  key={row.id} 
                  className={`data-table__row ${selectedRows.has(row.id) ? 'data-table__row--selected' : ''}`}
                >
                  <td className="data-table__cell data-table__cell--checkbox">
                    <input
                      type="checkbox"
                      checked={selectedRows.has(row.id)}
                      onChange={() => handleRowSelect(row.id)}
                      className="checkbox"
                    />
                  </td>
                  <td className="data-table__cell">
                    <div className="user-info">
                      <div className="user-info__avatar">
                        {row.name.split(' ').map(n => n[0]).join('')}
                      </div>
                      <div className="user-info__details">
                        <span className="user-info__name">{row.name}</span>
                        <span className="user-info__id">#{row.id}</span>
                      </div>
                    </div>
                  </td>
                  <td className="data-table__cell">{row.email}</td>
                  <td className="data-table__cell">
                    <span className="role-badge">{row.role}</span>
                  </td>
                  <td className="data-table__cell">
                    <span className={`status-badge ${getStatusColor(row.status)}`}>
                      {row.status}
                    </span>
                  </td>
                  <td className="data-table__cell">{row.lastLogin}</td>
                  <td className="data-table__cell">
                    <div className="score-indicator">
                      <div className="score-indicator__bar">
                        <div 
                          className="score-indicator__fill"
                          style={{ width: `${row.score}%` }}
                        />
                      </div>
                      <span className="score-indicator__text">{row.score}%</span>
                    </div>
                  </td>
                  <td className="data-table__cell">
                    <div className="table-actions">
                      <button className="table-actions__button">Edit</button>
                      <button className="table-actions__button table-actions__button--danger">Delete</button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        {/* Pagination */}
        <div className="pagination">
          <div className="pagination__info">
            <span>
              Showing {startIndex + 1} to {Math.min(endIndex, filteredAndSortedData.length)} of{' '}
              {filteredAndSortedData.length} entries
            </span>
            <select
              value={itemsPerPage}
              onChange={(e) => {
                setItemsPerPage(Number(e.target.value));
                setCurrentPage(1);
              }}
              className="pagination__select"
            >
              <option value={5}>5 per page</option>
              <option value={10}>10 per page</option>
              <option value={25}>25 per page</option>
              <option value={50}>50 per page</option>
            </select>
          </div>

          <div className="pagination__controls">
            <button
              className="pagination__button"
              onClick={() => setCurrentPage(1)}
              disabled={currentPage === 1}
            >
              <ChevronsLeft size={16} />
            </button>
            <button
              className="pagination__button"
              onClick={() => setCurrentPage(currentPage - 1)}
              disabled={currentPage === 1}
            >
              <ChevronLeft size={16} />
            </button>

            {/* Page Numbers */}
            <div className="pagination__pages">
              {/* Show first page if not in first few pages */}
              {currentPage > 3 && (
                <>
                  <button
                    className="pagination__page"
                    onClick={() => setCurrentPage(1)}
                  >
                    1
                  </button>
                  {currentPage > 4 && <span className="pagination__ellipsis">...</span>}
                </>
              )}
              
              {/* Show current page and surrounding pages */}
              {Array.from({ length: Math.min(5, totalPages) }, (_, i) => {
                const pageNumber = Math.max(1, Math.min(totalPages - 4, currentPage - 2)) + i;
                if (pageNumber > totalPages) return null;
                
                return (
                  <button
                    key={pageNumber}
                    className={`pagination__page ${currentPage === pageNumber ? 'pagination__page--active' : ''}`}
                    onClick={() => setCurrentPage(pageNumber)}
                  >
                    {pageNumber}
                  </button>
                );
              })}
              
              {/* Show last page if not in last few pages */}
              {currentPage < totalPages - 2 && (
                <>
                  {currentPage < totalPages - 3 && <span className="pagination__ellipsis">...</span>}
                  <button
                    className="pagination__page"
                    onClick={() => setCurrentPage(totalPages)}
                  >
                    {totalPages}
                  </button>
                </>
              )}
            </div>

            <button
              className="pagination__button"
              onClick={() => setCurrentPage(currentPage + 1)}
              disabled={currentPage === totalPages}
            >
              <ChevronRight size={16} />
            </button>
            <button
              className="pagination__button"
              onClick={() => setCurrentPage(totalPages)}
              disabled={currentPage === totalPages}
            >
              <ChevronsRight size={16} />
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};
