import React from 'react';
import { ConnectorType } from './connectorTypes';
import './ConnectorItem.scss';

interface ConnectorItemProps {
  connector: ConnectorType;
  isDragging?: boolean;
  onStartConnection?: () => void;
  showConnectionPoint?: boolean;
  isConnecting?: boolean;
  isConnectionSource?: boolean;
  canBeTarget?: boolean;
  isCompact?: boolean;
  onClick?: () => void;
}

export const ConnectorItem: React.FC<ConnectorItemProps> = ({
  connector,
  isDragging = false,
  onStartConnection,
  showConnectionPoint = false,
  isConnecting = false,
  isConnectionSource = false,
  canBeTarget = false,
  isCompact = false,
  onClick
}) => {
  const IconComponent = connector.icon;

  return (
    <div 
      className={`connector-item ${isDragging ? 'connector-item--dragging' : ''} ${canBeTarget ? 'connector-item--target' : ''} ${isConnectionSource ? 'connector-item--source' : ''} ${isCompact ? 'connector-item--compact' : ''}`}
      style={{
        '--connector-color': connector.color,
        '--connector-bg': connector.bgColor,
      } as React.CSSProperties}
      onClick={onClick}
    >
      <div className="connector-item__header">
        <div className="connector-item__icon">
          <IconComponent size={isCompact ? 16 : 20} />
        </div>
        <div className="connector-item__info">
          <h6 className="connector-item__name">{connector.name}</h6>
          {!isCompact && <span className="connector-item__category">{connector.category}</span>}
        </div>

        
        {/* Connection point */}
        {showConnectionPoint && (
          <button
            className="connector-item__connection-point"
            onClick={(e) => {
              e.stopPropagation();
              onStartConnection?.();
            }}
            title="Connect to another component"
          >
            <svg width="8" height="8" viewBox="0 0 12 12" fill="currentColor">
              <circle cx="6" cy="6" r="4" />
            </svg>
          </button>
        )}
      </div>
      
      {!isCompact && (
        <>
          <p className="connector-item__description">{connector.description}</p>
          
          <div className="connector-item__features">
            {connector.features.slice(0, 3).map((feature, index) => (
              <span key={index} className="connector-item__feature">
                {feature}
              </span>
            ))}
            {connector.features.length > 3 && (
              <span className="connector-item__feature connector-item__feature--more">
                +{connector.features.length - 3} more
              </span>
            )}
          </div>
        </>
      )}
      

    </div>
  );
};
