import React, { useEffect, useState, useMemo } from 'react';
import { useDroppable } from '@dnd-kit/core';
import { SortableConnector } from './SortableConnector';
import { SortableNote } from './SortableNote';
import { ConfigurationPanel } from './ConfigurationPanel';
import { ConnectorType } from './connectorTypes';
import './Canvas.scss';

interface CanvasConnector extends ConnectorType {
  id: string;
  x: number;
  y: number;
}

interface Connection {
  id: string;
  fromId: string;
  toId: string;
}

interface CanvasNote {
  id: string;
  x: number;
  y: number;
  content: string;
}

interface CanvasProps {
  connectors: CanvasConnector[];
  connections: Connection[];
  notes: CanvasNote[];
  onRemoveConnector: (id: string) => void;
  onStartConnection: (fromId: string) => void;
  onFinishConnection: (toId: string) => void;
  onCancelConnection: () => void;
  onConfigureConnector: (connector: CanvasConnector) => void;
  onAutoArrange?: () => void;
  onAddNote?: () => void;
  onUpdateNote?: (noteId: string, updates: { x?: number; y?: number; content?: string }) => void;
  onRemoveNote?: (noteId: string) => void;
  isConnecting: boolean;
  connectingFrom: string | null;
  draggedFromPalette: boolean;
  
  // Configuration panel props
  configurationPanel: {
    isOpen: boolean;
    connector: CanvasConnector | null;
  };
  onCloseConfigurationPanel: () => void;
  onSaveConfiguration: (connectorId: string, config: any) => void;
}

export const Canvas: React.FC<CanvasProps> = ({ 
  connectors, 
  connections,
  notes,
  onRemoveConnector, 
  onStartConnection, 
  onFinishConnection, 
  onCancelConnection,
  onConfigureConnector,
  onAutoArrange,
  onAddNote,
  onUpdateNote,
  onRemoveNote,
  isConnecting, 
  connectingFrom,
  draggedFromPalette,
  configurationPanel,
  onCloseConfigurationPanel,
  onSaveConfiguration
}) => {
  const { isOver, setNodeRef } = useDroppable({
    id: 'canvas',
  });

  
  // Force re-render when expansion state or connector positions change
  const [forceUpdateCounter, setForceUpdate] = useState(0);
  
  useEffect(() => {
    // Force re-render when connector positions change to update connections
    setForceUpdate(prev => prev + 1);
  }, [connectors, connections]);

  // Connection point calculation using state-based positions for reliability
  const createSmartConnectionPath = (fromConnector: any, toConnector: any) => {
    const GAP = 8; // Adjustable gap from connector edges
    
    // Use state-based positions directly for more reliable connection rendering
    const getConnectorBounds = (connector: any) => {
      const width = 140;
      const height = 80;
      
      // Always use the connector's state position for consistency
      return {
        left: connector.x,
        right: connector.x + width,
        top: connector.y,
        bottom: connector.y + height,
        centerX: connector.x + width / 2,
        centerY: connector.y + height / 2
      };
    };
    
    const fromBounds = getConnectorBounds(fromConnector);
    const toBounds = getConnectorBounds(toConnector);
    
    // Connection points with gaps
    const fromX = fromBounds.right + GAP;
    const fromY = fromBounds.centerY;
    const toX = toBounds.left - GAP;
    const toY = toBounds.centerY;
    
    // Check if we need special routing (target is to the left of source)
    const needsSpecialRouting = toX < fromX;
    
    if (!needsSpecialRouting) {
      // Simple left-to-right routing
      const midX = fromX + (toX - fromX) / 2;
      
      if (Math.abs(fromY - toY) < 10) {
        return `M ${fromX} ${fromY} L ${toX} ${toY}`;
      } else {
        return `
          M ${fromX} ${fromY}
          L ${midX - 4} ${fromY}
          Q ${midX} ${fromY} ${midX} ${fromY < toY ? fromY + 4 : fromY - 4}
          L ${midX} ${toY < fromY ? toY + 4 : toY - 4}
          Q ${midX} ${toY} ${midX + 4} ${toY}
          L ${toX} ${toY}
        `.replace(/\s+/g, ' ').trim();
      }
    }
    
    // Special routing: Find minimal collision-free path
    // Strategy: Find the shortest safe route by checking all possible paths
    
    // Define all 8 edges with collision zones (including gaps)
    const fromZone = {
      left: fromBounds.left - GAP,
      right: fromBounds.right + GAP,
      top: fromBounds.top - GAP,
      bottom: fromBounds.bottom + GAP
    };
    
    const toZone = {
      left: toBounds.left - GAP,
      right: toBounds.right + GAP,
      top: toBounds.top - GAP,
      bottom: toBounds.bottom + GAP
    };
    
    // Check if a point is inside any collision zone
    const isInCollisionZone = (x: number, y: number) => {
      return (x >= fromZone.left && x <= fromZone.right && y >= fromZone.top && y <= fromZone.bottom) ||
             (x >= toZone.left && x <= toZone.right && y >= toZone.top && y <= toZone.bottom);
    };
    
    // Find safe routing coordinates
    // Try different routing strategies and pick the shortest collision-free path
    
    const routingOptions = [];
    
    // Option 1: Route above both connectors
    const aboveY = Math.min(fromZone.top, toZone.top) - 20;
    if (aboveY > 20) { // Ensure we don't go too high
      const aboveTurnX = Math.min(fromZone.left, toZone.left) - 20;
      if (!isInCollisionZone(aboveTurnX, aboveY)) {
        routingOptions.push({
          type: 'above',
          routeY: aboveY,
          turnX: aboveTurnX,
          distance: Math.abs(fromY - aboveY) + Math.abs(aboveY - toY) + Math.abs(fromX - aboveTurnX) + Math.abs(aboveTurnX - toX)
        });
      }
    }
    
    // Option 2: Route below both connectors
    const belowY = Math.max(fromZone.bottom, toZone.bottom) + 20;
    const belowTurnX = Math.min(fromZone.left, toZone.left) - 20;
    if (!isInCollisionZone(belowTurnX, belowY)) {
      routingOptions.push({
        type: 'below',
        routeY: belowY,
        turnX: belowTurnX,
        distance: Math.abs(fromY - belowY) + Math.abs(belowY - toY) + Math.abs(fromX - belowTurnX) + Math.abs(belowTurnX - toX)
      });
    }
    
    // Option 3: Route between connectors (if there's vertical space)
    if (fromZone.bottom < toZone.top || toZone.bottom < fromZone.top) {
      let betweenY;
      if (fromZone.bottom < toZone.top) {
        betweenY = (fromZone.bottom + toZone.top) / 2;
      } else {
        betweenY = (toZone.bottom + fromZone.top) / 2;
      }
      
      const betweenTurnX = Math.min(fromZone.left, toZone.left) - 20;
      if (!isInCollisionZone(betweenTurnX, betweenY)) {
        routingOptions.push({
          type: 'between',
          routeY: betweenY,
          turnX: betweenTurnX,
          distance: Math.abs(fromY - betweenY) + Math.abs(betweenY - toY) + Math.abs(fromX - betweenTurnX) + Math.abs(betweenTurnX - toX)
        });
      }
    }
    
    // Pick the shortest collision-free route
    if (routingOptions.length === 0) {
      // Fallback: simple direct route with basic clearance
      const fallbackY = fromY < toY ? Math.max(fromZone.bottom, toZone.bottom) + 30 : Math.min(fromZone.top, toZone.top) - 30;
      const fallbackTurnX = Math.min(fromZone.left, toZone.left) - 30;
      
      return `
        M ${fromX} ${fromY}
        L ${fromX + 16} ${fromY}
        Q ${fromX + 20} ${fromY} ${fromX + 20} ${fromY < fallbackY ? fromY + 4 : fromY - 4}
        L ${fromX + 20} ${fallbackY < fromY ? fallbackY + 4 : fallbackY - 4}
        Q ${fromX + 20} ${fallbackY} ${fromX + 24} ${fallbackY}
        L ${fallbackTurnX - 4} ${fallbackY}
        Q ${fallbackTurnX} ${fallbackY} ${fallbackTurnX} ${fallbackY < toY ? fallbackY + 4 : fallbackY - 4}
        L ${fallbackTurnX} ${toY < fallbackY ? toY + 4 : toY - 4}
        Q ${fallbackTurnX} ${toY} ${fallbackTurnX + 4} ${toY}
        L ${toX} ${toY}
      `.replace(/\s+/g, ' ').trim();
    }
    
    // Use the shortest route
    const bestRoute = routingOptions.reduce((best, current) => 
      current.distance < best.distance ? current : best
    );
    
    const { routeY, turnX } = bestRoute;
    const arcCorner = (
        toX: number,
        toY: number,
        dx: number, dy: number,   // incoming vector
        nx: number, ny: number    // outgoing vector
    ) => {
      // cross product sign decides clockwise vs counterclockwise
      const cross = dx * ny - dy * nx;
      const sweep = cross > 0 ? 1 : 0;
      return `A 4 4 0 0 ${sweep} ${toX} ${toY}`;
    };
    
    // Build the optimal path with hardcoded 4px radius for proper 90-degree curves
    const verticalDirection = fromY < routeY ? 1 : -1;
    const finalDirection = routeY < toY ? 1 : -1;

    const targetTurnX = verticalDirection == -1 ? turnX : (toBounds.left - 44);

         return `
       M ${fromX} ${fromY}
       L ${fromX + 16} ${fromY}
       ${arcCorner(fromX + 20, fromY + (verticalDirection * 4), 1, 0, 0, verticalDirection)}
       L ${fromX + 20} ${routeY - (verticalDirection * 4)}
       ${arcCorner(fromX + 16, routeY, 0, -verticalDirection, 1, 0)}
       L ${targetTurnX + 4} ${routeY}
       ${arcCorner(targetTurnX, routeY + (finalDirection * 4), finalDirection, 0, 0, -1)}
       L ${targetTurnX} ${toY - (finalDirection * 4)}
       ${arcCorner(targetTurnX + 4, toY, 0, finalDirection, 1, 0)}
       L ${toX} ${toY}
     `.replace(/\s+/g, ' ').trim();
  };

  const renderedConnections = useMemo(() => {
    return connections.map(connection => {
      const fromConnector = connectors.find(c => c.id === connection.fromId);
      const toConnector = connectors.find(c => c.id === connection.toId);
      
      // Enhanced validation to ensure both connectors exist and have valid positions
      if (!fromConnector || !toConnector || 
          typeof fromConnector.x !== 'number' || typeof fromConnector.y !== 'number' ||
          typeof toConnector.x !== 'number' || typeof toConnector.y !== 'number') {
        // Only log in development to avoid console spam
        if (process.env.NODE_ENV === 'development') {
          console.warn(`Invalid connection: ${connection.id}`, { fromConnector, toConnector });
        }
        return null;
      }
      
      try {
        const pathData = createSmartConnectionPath(fromConnector, toConnector);
        
        return (
          <path
            key={connection.id}
            d={pathData}
            stroke="rgba(124, 58, 237, 0.8)"
            strokeWidth="2"
            fill="none"
            markerEnd="url(#arrowhead)"
            className="connection-path connection-path--connected"
          />
        );
      } catch (error) {
        console.error(`Error rendering connection ${connection.id}:`, error);
        return null;
      }
    });
  }, [connections, connectors, forceUpdateCounter]);

  const renderUnconnectedLines = () => {
    return connectors.map(connector => {
      const hasOutgoingConnection = connections.some(c => c.fromId === connector.id);
      if (hasOutgoingConnection) return null;
      
      // Show dotted line from unconnected connector with proper gap
      const GAP = 8;
      const connectorWidth = 140;
      const connectorHeight = 80;
      
      const fromX = connector.x + connectorWidth + GAP;
      const fromY = connector.y + connectorHeight / 2;
      const toX = fromX + 60;
      const toY = fromY;
      
      return (
        <g key={`unconnected-${connector.id}`}>
          <line
            x1={fromX}
            y1={fromY}
            x2={toX}
            y2={toY}
            stroke="rgba(124, 58, 237, 0.4)"
            strokeWidth="2"
            strokeDasharray="4,4"
            className="connection-path connection-path--unconnected"
          />
          {/* Draggable endpoint */}
          <circle
            cx={toX}
            cy={toY}
            r="6"
            fill="rgba(124, 58, 237, 0.6)"
            stroke="white"
            strokeWidth="2"
            className="connection-endpoint"
            style={{ cursor: 'pointer' }}
            onClick={(e) => {
              e.stopPropagation();
              onStartConnection(connector.id);
            }}
          />
        </g>
      );
    });
  };

  return (
    <div className="canvas-container">
      <div className="canvas-container__header">
        <div className="canvas-container__title">
          <h4>Integration Canvas</h4>
          <p>Drop connectors here to build your flow</p>
        </div>
        <div className="canvas-container__actions">
          {onAutoArrange && (
            <button 
              className="canvas-action-btn"
              onClick={onAutoArrange}
              title="Auto-arrange connectors"
            >
              <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <rect x="3" y="3" width="6" height="6" rx="1"/>
                <rect x="15" y="3" width="6" height="6" rx="1"/>
                <rect x="3" y="15" width="6" height="6" rx="1"/>
                <rect x="15" y="15" width="6" height="6" rx="1"/>
                <path d="M9 6h6"/>
                <path d="M9 18h6"/>
                <path d="M6 9v6"/>
                <path d="M18 9v6"/>
              </svg>
              Auto-arrange
            </button>
          )}
          {onAddNote && (
            <button 
              className="canvas-action-btn"
              onClick={onAddNote}
              title="Add note"
            >
              <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <path d="M14 2H6a2 2 0 0 0-2 2v16a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V8z"/>
                <polyline points="14,2 14,8 20,8"/>
                <line x1="16" y1="13" x2="8" y2="13"/>
                <line x1="16" y1="17" x2="8" y2="17"/>
                <polyline points="10,9 9,9 8,9"/>
              </svg>
              Add Note
            </button>
          )}
        </div>
      </div>
      
      <div
        id="canvas"
        ref={setNodeRef}
        className={`canvas ${isOver && draggedFromPalette ? 'canvas--drag-over' : ''} ${connectors.length === 0 && notes.length === 0 ? 'canvas--empty' : ''}`}
      >
        <div 
          className="canvas__scrollable-content"
          style={{
            // Dynamic width based on connector and note positions for expansion
            // Use max-width to prevent overflow beyond container
            minWidth: (connectors.length > 0 || notes.length > 0)
              ? Math.max(800, 
                  Math.max(
                    ...connectors.map(c => c.x + 140 + 100),
                    ...notes.map(n => n.x + 200 + 100), // Note width is 200px
                    0
                  )
                ) + 'px'
              : '800px',
            minHeight: (connectors.length > 0 || notes.length > 0)
              ? Math.max(500,
                  Math.max(
                    ...connectors.map(c => c.y + 100 + 50),
                    ...notes.map(n => n.y + 120 + 50), // Note height is 120px
                    0
                  )
                ) + 'px'
              : '500px',
            // Reduce available width when configuration panel is open
            paddingRight: configurationPanel.isOpen ? '320px' : '0',
            // Ensure content doesn't exceed canvas boundaries
            maxWidth: '100%',
            maxHeight: '100%', // Prevent content from exceeding canvas height
            overflow: 'visible' // Allow content to be scrollable within canvas
          }}
        >
        {connectors.length === 0 && notes.length === 0 ? (
          <div className="canvas__empty-state">
            <div className="canvas__empty-icon">
              <svg width="64" height="64" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="1">
                <path d="M21 16V8a2 2 0 0 0-1-1.73l-7-4a2 2 0 0 0-2 0l-7 4A2 2 0 0 0 3 8v8a2 2 0 0 0 1 1.73l7 4a2 2 0 0 0 2 0l7-4A2 2 0 0 0 21 16z"/>
                <polyline points="3.27,6.96 12,12.01 20.73,6.96"/>
                <line x1="12" y1="22.08" x2="12" y2="12"/>
              </svg>
            </div>
            <h5>Canvas is Empty</h5>
            <p>Drag connectors from the palette or add notes to start building your integration flow</p>
          </div>
        ) : (
          <>
            {/* Connectors and Notes in the same draggable area */}
            <div className="canvas__items">
              {connectors.map((connector) => (
                <SortableConnector
                  key={connector.id}
                  connector={connector}
                  onRemove={onRemoveConnector}
                  onStartConnection={onStartConnection}
                  onFinishConnection={onFinishConnection}
                  onConfigureConnector={onConfigureConnector}
                  isConnecting={isConnecting}
                  connectingFrom={connectingFrom}
                />
              ))}
              
              {notes.map((note) => (
                <SortableNote
                  key={note.id}
                  note={note}
                  onUpdate={onUpdateNote!}
                  onRemove={onRemoveNote!}
                />
              ))}
            </div>
            
            {/* Connection lines SVG overlay */}
            <svg 
              className="canvas__connections" 
              width="100%" 
              height="100%"
              style={{
                // Ensure SVG covers the entire scrollable content area
                minWidth: (connectors.length > 0 || notes.length > 0)
                  ? Math.max(800, 
                      Math.max(
                        ...connectors.map(c => c.x + 140 + 100),
                        ...notes.map(n => n.x + 200 + 100),
                        0
                      )
                    ) + 'px'
                  : '800px',
                minHeight: (connectors.length > 0 || notes.length > 0)
                  ? Math.max(500,
                      Math.max(
                        ...connectors.map(c => c.y + 100 + 50),
                        ...notes.map(n => n.y + 120 + 50),
                        0
                      )
                    ) + 'px'
                  : '500px'
              }}
            >
              <defs>
                <marker
                  id="arrowhead"
                  markerWidth="10"
                  markerHeight="7"
                  refX="9"
                  refY="3.5"
                  orient="auto"
                >
                  <polygon
                    points="0 0, 10 3.5, 0 7"
                    fill="rgba(124, 58, 237, 0.6)"
                  />
                </marker>
              </defs>
              {renderedConnections}
              {renderUnconnectedLines()}
              
              {/* Active connection line while dragging */}
              {isConnecting && connectingFrom && (() => {
                const sourceConnector = connectors.find(c => c.id === connectingFrom);
                if (!sourceConnector) return null;
                
                const GAP = 8;
                const connectorWidth = 140;
                const connectorHeight = 80;
                
                const fromX = sourceConnector.x + connectorWidth + GAP;
                const fromY = sourceConnector.y + connectorHeight / 2;
                
                return (
                  <line
                    x1={fromX}
                    y1={fromY}
                    x2={fromX + 60}
                    y2={fromY}
                    stroke="rgba(124, 58, 237, 0.8)"
                    strokeWidth="2"
                    strokeDasharray="8,4"
                    className="connection-path connection-path--active"
                    markerEnd="url(#arrowhead)"
                  />
                );
              })()}
            </svg>
          </>
        )}
        
        {/* Grid pattern overlay */}
        <div className="canvas__grid"></div>

        {/* Configuration Panel */}
        {configurationPanel.isOpen && (
          <ConfigurationPanel
            connector={configurationPanel.connector}
            isOpen={configurationPanel.isOpen}
            onClose={onCloseConfigurationPanel}
            onSave={onSaveConfiguration}
          />
        )}
        </div>
      </div>
    </div>
  );
};
