import React, { useState, useEffect } from 'react';
import { GameConfig } from '../UndercoverGame';
import PerplexityService from '../services/PerplexityService';
import wordsData from '../wordpool/words.json';
import './AssignmentPhase.scss';

export interface PlayerAssignment {
  name: string;
  role: 'regular' | 'undercover' | 'mrwhite';
  word?: string;
  isAlive?: boolean;
}

interface AssignmentPhaseProps {
  gameConfig: GameConfig;
  onPhaseComplete: (assignments: PlayerAssignment[]) => void;
}

interface GameWords {
  regularWord: string;
  undercoverWords: string[];
  selectedUndercoverWord: string;
}

export const AssignmentPhase: React.FC<AssignmentPhaseProps> = ({ gameConfig, onPhaseComplete }) => {
  const [currentPlayerIndex, setCurrentPlayerIndex] = useState(0);
  const [isWordRevealed, setIsWordRevealed] = useState(false);
  const [showNextPlayer, setShowNextPlayer] = useState(false);
  const [playerAssignments, setPlayerAssignments] = useState<PlayerAssignment[]>([]);
  const [, setGameWords] = useState<GameWords | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [loadingMessage, setLoadingMessage] = useState('Preparing your game...');
  const [hasError, setHasError] = useState(false);

  // Initialize game assignments
  useEffect(() => {
    const initializeGame = async () => {
      try {
        setIsLoading(true);
        setHasError(false);
        setLoadingMessage('Selecting words from categories...');
        
        // Get available words from selected categories
        const availableWords: string[] = [];
        Object.entries(gameConfig.selectedCategories).forEach(([category, isSelected]) => {
          if (isSelected && wordsData[category as keyof typeof wordsData]) {
            availableWords.push(...wordsData[category as keyof typeof wordsData]);
          }
        });
        
        if (availableWords.length === 0) {
          throw new Error('No words available from selected categories');
        }
        
        // Select random regular word
        const regularWord = availableWords[Math.floor(Math.random() * availableWords.length)];
        
        setLoadingMessage('Generating pair of words with AI...');
        
        // Generate 2 additional words using PerplexityService (works for all environments)
        const additionalWords = await PerplexityService.generateUndercoverWords(regularWord);
        
        // Now we have 3 total words: regularWord, word1, word2
        // Randomly select the non-regular word between word1 and word2
        const nonRegularWord = additionalWords[Math.floor(Math.random() * additionalWords.length)];
        

        const gameWordsData: GameWords = {
          regularWord,
          undercoverWords: additionalWords,
          selectedUndercoverWord: nonRegularWord
        };
        
        setGameWords(gameWordsData);
        
        setLoadingMessage('Assigning roles to players...');
        
        // Assign roles to players
        const assignments = assignRoles(gameConfig, gameWordsData);
        setPlayerAssignments(assignments);
        setIsLoading(false);
      } catch (error) {
        console.error('Error initializing game:', error);
        setHasError(true);
        const errorMessage = error instanceof Error ? error.message : String(error);
        setLoadingMessage(` Failed to generate words: ${errorMessage}`);
        setIsLoading(false);
      }
    };

    initializeGame();
  }, [gameConfig]);

  const handleRetry = () => {
    // Trigger re-initialization by updating a dependency
    setIsLoading(true);
    setHasError(false);
    setLoadingMessage('Preparing your game...');
    
    // Re-run the initialization
    const initializeGame = async () => {
      try {
        setLoadingMessage('Selecting words from categories...');
        
        // Get available words from selected categories
        const availableWords: string[] = [];
        Object.entries(gameConfig.selectedCategories).forEach(([category, isSelected]) => {
          if (isSelected && wordsData[category as keyof typeof wordsData]) {
            availableWords.push(...wordsData[category as keyof typeof wordsData]);
          }
        });
        
        if (availableWords.length === 0) {
          throw new Error('No words available from selected categories');
        }
        
        // Select random regular word
        const regularWord = availableWords[Math.floor(Math.random() * availableWords.length)];
        
        setLoadingMessage('Generating pair of words with AI...');
        
        // Generate 2 additional words using PerplexityService (works for all environments)
        const additionalWords = await PerplexityService.generateUndercoverWords(regularWord);
        
        // Now we have 3 total words: regularWord, word1, word2
        // Randomly select the non-regular word between word1 and word2
        const nonRegularWord = additionalWords[Math.floor(Math.random() * additionalWords.length)];

        const gameWordsData: GameWords = {
          regularWord,
          undercoverWords: additionalWords,
          selectedUndercoverWord: nonRegularWord
        };
        
        setGameWords(gameWordsData);
        
        setLoadingMessage('Assigning roles to players...');
        
        // Assign roles to players
        const assignments = assignRoles(gameConfig, gameWordsData);
        setPlayerAssignments(assignments);
        setIsLoading(false);
      } catch (error) {
        console.error('Error initializing game:', error);
        setHasError(true);
        const errorMessage = error instanceof Error ? error.message : String(error);
        setLoadingMessage(` Failed to generate words: ${errorMessage}`);
        setIsLoading(false);
      }
    };

    initializeGame();
  };

  const assignRoles = (config: GameConfig, gameWordsData: GameWords): PlayerAssignment[] => {
    const assignments: PlayerAssignment[] = [];
    const playerIndices = Array.from({ length: config.numPlayers }, (_, i) => i);
    
    // Shuffle player indices for random role assignment
    for (let i = playerIndices.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [playerIndices[i], playerIndices[j]] = [playerIndices[j], playerIndices[i]];
    }
    
    // Calculate the number of civilians (non-Mr.White players) - used for game logic validation
    
    // Randomly decide which word goes to which group (unpredictable pattern)
    const wordAssignments = Math.random() < 0.5 
      ? { civilians: gameWordsData.regularWord, undercovers: gameWordsData.selectedUndercoverWord }
      : { civilians: gameWordsData.selectedUndercoverWord, undercovers: gameWordsData.regularWord };

    // Assign Mr. Whites first (they don't get words)
    for (let i = 0; i < config.numMrWhites; i++) {
      const playerIndex = playerIndices[i];
      assignments[playerIndex] = {
        name: config.playerNames[playerIndex],
        role: 'mrwhite'
      };
    }
    
    // Assign undercovers (they get their assigned word)
    for (let i = config.numMrWhites; i < config.numMrWhites + config.numUndercovers; i++) {
      const playerIndex = playerIndices[i];
      assignments[playerIndex] = {
        name: config.playerNames[playerIndex],
        role: 'undercover',
        word: wordAssignments.undercovers
      };
    }
    
    // Assign regular players/civilians (they get their assigned word)
    for (let i = config.numMrWhites + config.numUndercovers; i < config.numPlayers; i++) {
      const playerIndex = playerIndices[i];
      assignments[playerIndex] = {
        name: config.playerNames[playerIndex],
        role: 'regular',
        word: wordAssignments.civilians
      };
    }
    
    // Fill any remaining slots (safety check)
    for (let i = 0; i < config.numPlayers; i++) {
      if (!assignments[i]) {
        assignments[i] = {
          name: config.playerNames[i],
          role: 'regular',
          word: wordAssignments.civilians
        };
      }
    }
    
    return assignments;
  };

  const handleRevealWord = () => {
    setIsWordRevealed(true);

    // If we're showing next player preview, actually move to next player
    if (showNextPlayer && currentPlayerIndex < gameConfig.numPlayers - 1) {
      setCurrentPlayerIndex(currentPlayerIndex + 1);
      setShowNextPlayer(false);
    }
  };

  const handleNextPlayer = () => {
    if (currentPlayerIndex < gameConfig.numPlayers - 1) {
      // Show preview of next player instead of immediately changing
      setShowNextPlayer(true);
      setIsWordRevealed(false);
    } else {
      // Last player - proceed to game
      onPhaseComplete(playerAssignments);
    }
  };

  if (isLoading || hasError) {
    return (
      <div className="assignment-phase assignment-phase--loading">
        <div className="loading-spinner">
          {!hasError && <div className="spinner"></div>}
          <p>{loadingMessage}</p>
          {loadingMessage.includes('AI') && !hasError && (
            <div className="ai-indicator">
              <span className="ai-icon"></span>
            </div>
          )}
          {hasError && (
            <div className="error-actions">
              <button className="retry-btn" onClick={handleRetry}>
                 Try Again
              </button>
              <p className="error-help">
                Make sure you have an internet connection and the Vercel API is accessible.
              </p>
            </div>
          )}
        </div>
      </div>
    );
  }

  // Show next player if preview is enabled, otherwise show current player
  const displayPlayerIndex = showNextPlayer ? Math.min(currentPlayerIndex + 1, gameConfig.numPlayers - 1) : currentPlayerIndex;
  const currentPlayer = playerAssignments[displayPlayerIndex];
  const isLastPlayer = currentPlayerIndex === gameConfig.numPlayers - 1;

  return (
    <div className="assignment-phase">
      <div className="assignment-phase__header">
        <h2 className="assignment-phase__title">Word Assignment</h2>
        <div className="assignment-phase__progress">
          <span className="progress-text">
            {showNextPlayer ? `Preview: Player ${displayPlayerIndex + 1}` : `Player ${displayPlayerIndex + 1}`} of {gameConfig.numPlayers}
          </span>
          <div className="progress-bar">
            <div
              className="progress-bar__fill"
              style={{ width: `${((displayPlayerIndex + 1) / gameConfig.numPlayers) * 100}%` }}
            />
          </div>
        </div>
      </div>

      <div className="assignment-phase__content">
        <div className="player-card">
          <div className="player-card__header">
            <h3 className="player-card__name">{currentPlayer?.name}</h3>
            {currentPlayer?.role === 'mrwhite' && isWordRevealed && (
              <div className={`role-indicator role-indicator--${currentPlayer?.role}`}>
                 Mr. White
              </div>
            )}
          </div>

          <div className="word-reveal-section">
            <div className="word-container">
              <div className={`word-curtain ${isWordRevealed ? 'word-curtain--revealed' : ''}`}>
                <div className="word-curtain__front">
                  <div className="curtain-icon"></div>
                  <p>Your secret word is hidden</p>
                  <button 
                    className="reveal-btn"
                    onClick={handleRevealWord}
                    disabled={isWordRevealed}
                  >
                    Reveal Word
                  </button>
                </div>
                <div className="word-curtain__back">
                  {isWordRevealed && currentPlayer?.role === 'mrwhite' && (
                    <div className="mr-white-message">
                      <div className="mr-white-message__icon"></div>
                      <h4 className="mr-white-message__title">You are Mr. White</h4>
                      <p className="mr-white-message__description">
                        You don't know the word. Listen carefully to others and try to blend in!
                      </p>
                    </div>
                  )}
                  {isWordRevealed && currentPlayer?.role !== 'mrwhite' && (
                    <div className="revealed-word">
                      <div className="word-icon">
                        
                      </div>
                      <div className="word-text">{currentPlayer?.word}</div>
                      <div className="word-role">
                        Your Secret Word
                      </div>
                    </div>
                  )}
                  {!isWordRevealed && (
                    <div className="revealed-word">
                      <div className="word-icon">
                        
                      </div>
                      <div className="word-text"></div>
                      <div className="word-role">
                        Your Secret Word
                      </div>
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>

          <div className="assignment-phase__actions">
            {showNextPlayer ? (
              <div className="preview-message">
                <p> Previewing next player - click "Reveal" to proceed</p>
              </div>
            ) : (
              <button
                className="next-btn"
                onClick={handleNextPlayer}
                disabled={!isWordRevealed}
              >
                {isLastPlayer && !showNextPlayer ? 'Start Game' : 'Next Player'}
                <span className="next-btn__icon">
                  {isLastPlayer && !showNextPlayer ? '' : ''}
                </span>
              </button>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};
