import React, { useState, useRef } from 'react';
import {
  DndContext,
  closestCenter,
  KeyboardSensor,
  PointerSensor,
  useSensor,
  useSensors,
  DragEndEvent,
  Modifier,
} from '@dnd-kit/core';
import {
  arrayMove,
  SortableContext,
  sortableKeyboardCoordinates,
  horizontalListSortingStrategy,
} from '@dnd-kit/sortable';
import { SortableTab } from './SortableTab';
import { TabContent } from './TabContent';
import { tabsData, TabData } from './tabsData';
import './RearrangeableTabs.scss';

export const RearrangeableTabs: React.FC = () => {
  const [tabs, setTabs] = useState<TabData[]>(tabsData);
  const [activeTab, setActiveTab] = useState<string>(tabs[0]?.id || '');
  const [pinnedTabs, setPinnedTabs] = useState<Set<string>>(new Set());
  const tabListRef = useRef<HTMLDivElement>(null);

  const sensors = useSensors(
    useSensor(PointerSensor, {
      activationConstraint: {
        distance: 8,
      },
    }),
    useSensor(KeyboardSensor, {
      coordinateGetter: sortableKeyboardCoordinates,
    })
  );

  // Boundary constraint modifier to keep tabs within container bounds and respect pinned tab boundaries
  const restrictToHorizontalAxis: Modifier = ({ transform, containerNodeRect, draggingNodeRect, active }) => {
    if (!containerNodeRect || !draggingNodeRect || !active) {
      return {
        ...transform,
        y: 0, // Always constrain Y to 0
      };
    }

    // If the active tab is pinned, don't allow any movement
    if (pinnedTabs.has(active.id as string)) {
      return {
        ...transform,
        x: 0,
        y: 0,
      };
    }

    // Calculate the boundaries
    const containerLeft = containerNodeRect.left;
    const containerRight = containerNodeRect.right;
    
    // Find the rightmost pinned tab to establish left boundary for unpinned tabs
    let leftBoundaryOffset = 0;
    if (pinnedTabs.size > 0 && tabListRef.current) {
      // Count pinned tabs and calculate their total width
      const pinnedCount = Array.from(pinnedTabs).length;
      const tabElements = tabListRef.current.querySelectorAll('.sortable-tab');
      
      if (tabElements.length > 0 && pinnedCount > 0) {
        // Calculate the right edge of the last pinned tab
        let pinnedTabsWidth = 0;
        for (let i = 0; i < Math.min(pinnedCount, tabElements.length); i++) {
          const tabElement = tabElements[i] as HTMLElement;
          pinnedTabsWidth += tabElement.offsetWidth;
        }
        leftBoundaryOffset = pinnedTabsWidth;
      }
    }
    
    // Calculate the min and max X positions
    const minX = (containerLeft + leftBoundaryOffset) - draggingNodeRect.left;
    const maxX = containerRight - draggingNodeRect.right;
    
    // Constrain the X transform within bounds (respecting pinned tab boundary)
    const constrainedX = Math.max(minX, Math.min(maxX, transform.x));
    
    return {
      ...transform,
      x: constrainedX,
      y: 0, // Always lock Y axis
    };
  };

  const handleDragEnd = (event: DragEndEvent) => {
    const { active, over } = event;

    if (over && active.id !== over.id) {
      // Don't allow reordering if the active tab is pinned
      if (pinnedTabs.has(active.id as string)) return;
      
      setTabs((items) => {
        const oldIndex = items.findIndex((item) => item.id === active.id);
        const newIndex = items.findIndex((item) => item.id === over.id);
        
        // Calculate the total number of pinned tabs (they should always be at the beginning)
        const totalPinnedCount = Array.from(pinnedTabs).length;
        
        // Don't allow moving unpinned tabs to positions before all pinned tabs
        // Ensure the new position is at least after all pinned tabs
        const adjustedNewIndex = Math.max(newIndex, totalPinnedCount);

        return arrayMove(items, oldIndex, adjustedNewIndex);
      });
    }
  };

  const handleTabClick = (tabId: string) => {
    setActiveTab(tabId);
  };

  // Removed handleRemoveTab as we no longer use close buttons

  const handlePinTab = (tabId: string) => {
    setPinnedTabs(prev => {
      const newPinned = new Set(prev);
      if (newPinned.has(tabId)) {
        // Unpinning - remove from pinned set and move to first unpinned position
        newPinned.delete(tabId);
        
        // Move the unpinned tab to the first position after all remaining pinned tabs
        setTabs(currentTabs => {
          const tabIndex = currentTabs.findIndex(tab => tab.id === tabId);
          if (tabIndex >= 0) {
            const tabToMove = currentTabs[tabIndex];
            const newTabs = [...currentTabs];
            newTabs.splice(tabIndex, 1);
            
            // Calculate the number of remaining pinned tabs (excluding the one being unpinned)
            const remainingPinnedCount = Array.from(newPinned).length;
            
            // Insert the unpinned tab right after all pinned tabs
            newTabs.splice(remainingPinnedCount, 0, tabToMove);
            
            return newTabs;
          }
          return currentTabs;
        });
      } else {
        // Pinning - add to pinned set and move to front
        newPinned.add(tabId);
        
        // Move the pinned tab to the front
        setTabs(currentTabs => {
          const tabIndex = currentTabs.findIndex(tab => tab.id === tabId);
          if (tabIndex > 0) {
            const tabToMove = currentTabs[tabIndex];
            const newTabs = [...currentTabs];
            newTabs.splice(tabIndex, 1);
            
            // Find the position after all other pinned tabs
            const pinnedCount = Array.from(newPinned).filter(id => id !== tabId).length;
            newTabs.splice(pinnedCount, 0, tabToMove);
            
            return newTabs;
          }
          return currentTabs;
        });
      }
      return newPinned;
    });
  };

  const activeTabData = tabs.find(tab => tab.id === activeTab);

  return (
    <div className="rearrangeable-tabs">
      <div className="rearrangeable-tabs__header">
        <div>
          <h3>Interactive Tabs</h3>
          <p>Drag tabs to reorder them, click to switch content</p>
        </div>
        <div className="rearrangeable-tabs__stats">
          <span className="rearrangeable-tabs__count">
            {tabs.length} tab{tabs.length !== 1 ? 's' : ''}
          </span>
        </div>
      </div>

      <div className="rearrangeable-tabs__container">
        <DndContext
          sensors={sensors}
          collisionDetection={closestCenter}
          onDragEnd={handleDragEnd}
          modifiers={[restrictToHorizontalAxis]}
        >
          <div className="rearrangeable-tabs__nav">
            <SortableContext 
              items={tabs.map(tab => tab.id)} 
              strategy={horizontalListSortingStrategy}
            >
              <div className="rearrangeable-tabs__list" ref={tabListRef}>
                {tabs.map((tab) => (
                  <SortableTab
                    key={tab.id}
                    tab={tab}
                    isActive={activeTab === tab.id}
                    isPinned={pinnedTabs.has(tab.id)}
                    onClick={() => handleTabClick(tab.id)}
                    onPin={() => handlePinTab(tab.id)}
                  />
                ))}
              </div>
            </SortableContext>
          </div>
        </DndContext>

        <div className="rearrangeable-tabs__content">
          {activeTabData && (
            <TabContent 
              key={activeTabData.id}
              tab={activeTabData} 
            />
          )}
        </div>
      </div>
    </div>
  );
};
