import React, { useState, useEffect } from 'react';
import { GameConfig } from '../UndercoverGame';
import './SetupPhase.scss';

interface SetupPhaseProps {
  onStartGame: (config: GameConfig) => void;
}

// Role distribution based on README specifications
const getRoleDistribution = (numPlayers: number) => {
  if (numPlayers <= 4) return { undercovers: 1, mrWhites: 0 };
  if (numPlayers <= 6) return { undercovers: 1, mrWhites: 1 };
  if (numPlayers <= 10) return { undercovers: 2, mrWhites: 1 };
  if (numPlayers <= 15) return { undercovers: 3, mrWhites: 1 };
  return { undercovers: 4, mrWhites: 1 };
};

export const SetupPhase: React.FC<SetupPhaseProps> = ({ onStartGame }) => {
  const [numPlayers, setNumPlayers] = useState(5);
  const [customRoles, setCustomRoles] = useState(false);
  const [numUndercovers, setNumUndercovers] = useState(1);
  const [numMrWhites, setNumMrWhites] = useState(1);
  const [playerNames, setPlayerNames] = useState<string[]>(['']);
  const [selectedCategories, setSelectedCategories] = useState({
    objects: true,
    foods: true,
    animals: true,
    abstracts: true,
    places: true
  });

  // Auto-update role distribution when player count changes
  useEffect(() => {
    if (!customRoles) {
      const distribution = getRoleDistribution(numPlayers);
      setNumUndercovers(distribution.undercovers);
      setNumMrWhites(distribution.mrWhites);
    }
  }, [numPlayers, customRoles]);

  // Update player names array when player count changes
  useEffect(() => {
    const newNames = Array(numPlayers).fill('').map((_, index) => 
      playerNames[index] || `Player ${index + 1}`
    );
    setPlayerNames(newNames);
  }, [numPlayers]);

  const handlePlayerCountChange = (count: number) => {
    if (count >= 3 && count <= 20) {
      setNumPlayers(count);
    }
  };

  const handlePlayerNameChange = (index: number, name: string) => {
    const newNames = [...playerNames];
    newNames[index] = name;
    setPlayerNames(newNames);
  };

  const handleCategoryToggle = (category: keyof typeof selectedCategories) => {
    const activeCategories = Object.values(selectedCategories).filter(Boolean).length;
    
    // Prevent turning off the last active category
    if (activeCategories === 1 && selectedCategories[category]) {
      return;
    }
    
    setSelectedCategories(prev => ({
      ...prev,
      [category]: !prev[category]
    }));
  };

  const handleStartGame = () => {
    const config: GameConfig = {
      numPlayers,
      numUndercovers,
      numMrWhites,
      playerNames: playerNames.filter(name => name.trim() !== ''),
      selectedCategories
    };
    onStartGame(config);
  };

  const isValidSetup = () => {
    const totalSpecialRoles = numUndercovers + numMrWhites;
    const regularPlayers = numPlayers - totalSpecialRoles;
    const validPlayerNames = playerNames.filter(name => name.trim() !== '').length === numPlayers;
    
    return (
      numPlayers >= 3 && 
      numPlayers <= 20 && 
      // Must have at least 1 regular player
      totalSpecialRoles < numPlayers &&
      // Undercovers must be minority (less than regular players)
      numUndercovers < regularPlayers &&
      // Mr. White count cannot exceed undercovers
      numMrWhites <= numUndercovers &&
      // All player names must be filled
      validPlayerNames
    );
  };

  const regularPlayers = numPlayers - numUndercovers - numMrWhites;

  return (
    <div className="setup-phase">
      <div className="setup-phase__header">
        <h2 className="setup-phase__title"> Undercover Game Setup</h2>
        <p className="setup-phase__description">
          Configure your game settings and player names to get started
        </p>
      </div>

      <div className="setup-form">
        {/* Word Categories */}
        <div className="setup-section">
          <h3 className="setup-section__title">
            <span className="setup-section__icon"></span>
            Word Categories
          </h3>
          <p className="setup-section__note">Choose which categories to include in the game (minimum 1 required)</p>
          
          <div className="category-toggles">
            {Object.entries({
              objects: { name: 'Objects & Items', icon: '' },
              foods: { name: 'Food & Drinks', icon: '' },
              animals: { name: 'Animals & Nature', icon: '' },
              abstracts: { name: 'Abstract Concepts', icon: '' },
              places: { name: 'Places & Locations', icon: '' }
            }).map(([key, category]) => {
              const isActive = selectedCategories[key as keyof typeof selectedCategories];
              const activeCount = Object.values(selectedCategories).filter(Boolean).length;
              const isLastActive = activeCount === 1 && isActive;
              
              return (
                <button
                  key={key}
                  className={`category-toggle ${isActive ? 'category-toggle--active' : ''} ${isLastActive ? 'category-toggle--locked' : ''}`}
                  onClick={() => handleCategoryToggle(key as keyof typeof selectedCategories)}
                  disabled={isLastActive}
                >
                  <span className="category-toggle__icon">{category.icon}</span>
                  <span className="category-toggle__name">{category.name}</span>
                  {isLastActive && <span className="category-toggle__lock"></span>}
                </button>
              );
            })}
          </div>
        </div>

        {/* Player Count Configuration */}
        <div className="setup-section">
          <h3 className="setup-section__title">
            <span className="setup-section__icon"></span>
            Number of Players
          </h3>
          <div className="player-count-selector">
            <button
              className="count-btn count-btn--decrease"
              onClick={() => handlePlayerCountChange(numPlayers - 1)}
              disabled={numPlayers <= 3}
            >
              <span></span>
            </button>
            <div className="count-display">
              <span className="count-number">{numPlayers}</span>
              <span className="count-label">Players</span>
            </div>
            <button
              className="count-btn count-btn--increase"
              onClick={() => handlePlayerCountChange(numPlayers + 1)}
              disabled={numPlayers >= 20}
            >
              <span>+</span>
            </button>
          </div>
          <p className="setup-section__note">Choose between 3-20 players</p>
        </div>

        {/* Role Distribution */}
        <div className="setup-section">
          <div className="setup-section__header">
            <h3 className="setup-section__title">
              <span className="setup-section__icon"></span>
              Role Distribution
            </h3>
            <label className="custom-toggle">
              <input
                type="checkbox"
                checked={customRoles}
                onChange={(e) => setCustomRoles(e.target.checked)}
              />
              <span className="custom-toggle__slider"></span>
              <span className="custom-toggle__label">Custom</span>
            </label>
          </div>

          <div className="role-grid">
            <div className="role-card role-card--regular">
              <div className="role-card__icon"></div>
              <div className="role-card__content">
                <h4 className="role-card__title">Regular Players</h4>
                <div className="role-card__count">{regularPlayers}</div>
                <p className="role-card__description">Know the real word</p>
              </div>
            </div>

            <div className="role-card role-card--undercover">
              <div className="role-card__icon"></div>
              <div className="role-card__content">
                <h4 className="role-card__title">Undercovers</h4>
                <div className="role-card__count-control">
                  {customRoles ? (
                    <div className="mini-counter">
                      <button
                        onClick={() => setNumUndercovers(Math.max(1, numUndercovers - 1))}
                        disabled={numUndercovers <= 1}
                      >
                        
                      </button>
                      <span>{numUndercovers}</span>
                      <button
                        onClick={() => setNumUndercovers(numUndercovers + 1)}
                        disabled={
                          // Undercovers must be < regular players (minority rule)
                          // After increment: newUndercovers >= newRegularPlayers is invalid
                          (numUndercovers + 1) >= (numPlayers - (numUndercovers + 1) - numMrWhites) ||
                          // Total special roles must leave at least 1 regular player
                          (numUndercovers + 1) + numMrWhites >= numPlayers - 1
                        }
                      >
                        +
                      </button>
                    </div>
                  ) : (
                    <div className="role-card__count">{numUndercovers}</div>
                  )}
                </div>
                <p className="role-card__description">Have a similar word</p>
              </div>
            </div>

            <div className="role-card role-card--mrwhite">
              <div className="role-card__icon"></div>
              <div className="role-card__content">
                <h4 className="role-card__title">Mr. White</h4>
                <div className="role-card__count-control">
                  {customRoles ? (
                    <div className="mini-counter">
                      <button
                        onClick={() => setNumMrWhites(Math.max(0, numMrWhites - 1))}
                        disabled={numMrWhites <= 0}
                      >
                        
                      </button>
                      <span>{numMrWhites}</span>
                      <button
                        onClick={() => setNumMrWhites(numMrWhites + 1)}
                        disabled={
                          // Mr. White count cannot exceed undercovers
                          (numMrWhites + 1) > numUndercovers ||
                          // Total special roles must leave at least 1 regular player
                          numUndercovers + (numMrWhites + 1) >= numPlayers - 1 ||
                          // Ensure undercovers remain minority after Mr. White increment
                          numUndercovers >= (numPlayers - numUndercovers - (numMrWhites + 1))
                        }
                      >
                        +
                      </button>
                    </div>
                  ) : (
                    <div className="role-card__count">{numMrWhites}</div>
                  )}
                </div>
                <p className="role-card__description">Doesn't know any word</p>
              </div>
            </div>
          </div>
        </div>

        {/* Player Names */}
        <div className="setup-section">
          <h3 className="setup-section__title">
            <span className="setup-section__icon"></span>
            Player Names
          </h3>
          <div className="player-names-grid">
            {playerNames.map((name, index) => (
              <div key={index} className="player-input">
                <label className="player-input__label">Player {index + 1}</label>
                <input
                  type="text"
                  className="player-input__field"
                  value={name}
                  onChange={(e) => handlePlayerNameChange(index, e.target.value)}
                  placeholder={`Player ${index + 1}`}
                />
              </div>
            ))}
          </div>
        </div>

        {/* Validation Feedback */}
        {!isValidSetup() && (
          <div className="validation-feedback">
            <h4 className="validation-feedback__title"> Setup Requirements</h4>
            <ul className="validation-feedback__list">
              {numPlayers < 3 && <li>Need at least 3 players</li>}
              {numPlayers > 20 && <li>Maximum 20 players allowed</li>}
              {numUndercovers >= regularPlayers && <li>Undercovers must be minority (less than regular players)</li>}
              {numMrWhites > numUndercovers && <li>Mr. White count cannot exceed undercovers</li>}
              {numUndercovers + numMrWhites >= numPlayers && <li>Must have at least 1 regular player</li>}
              {playerNames.filter(name => name.trim() !== '').length !== numPlayers && <li>All player names must be filled</li>}
            </ul>
          </div>
        )}

        {/* Start Game Button */}
        <div className="setup-actions">
          <button
            className={`start-game-btn ${isValidSetup() ? 'start-game-btn--ready' : ''}`}
            onClick={handleStartGame}
            disabled={!isValidSetup()}
          >
            <span className="start-game-btn__icon"></span>
            <span className="start-game-btn__text">Start Game</span>
            {isValidSetup() && (
              <div className="start-game-btn__sparkles">
                <span className="sparkle"></span>
                <span className="sparkle"></span>
                <span className="sparkle"></span>
              </div>
            )}
          </button>
        </div>
      </div>
    </div>
  );
};
