import React, { useState, useCallback, useRef, useEffect } from 'react';
import {
  DndContext,
  DragEndEvent,
  DragMoveEvent,
  DragOverlay,
  DragStartEvent,
  MouseSensor,
  TouchSensor,
  useSensor,
  useSensors,
  pointerWithin,
} from '@dnd-kit/core';
// Removed unused arrayMove import
import { ConnectorPalette } from './ConnectorPalette';
import { Canvas } from './Canvas';
import { ConnectorItem } from './ConnectorItem';
import { connectorTypes, ConnectorType } from './connectorTypes';
import './DragDropCanvas.scss';

interface CanvasConnector extends ConnectorType {
  id: string;
  x: number;
  y: number;
}

interface Connection {
  id: string;
  fromId: string;
  toId: string;
}

// Initialize with default connectors and connections for demo
