const getDefaultNotes = () => {
  return [
    {
      id: 'demo-note-1',
      x: 400,
      y: 200,
      content: 'Sample GenAI integration flow'
    }
  ];
};

export const DragDropCanvas: React.FC = () => {
  const [canvasConnectors, setCanvasConnectors] = useState<CanvasConnector[]>(getDefaultConnectors());
  const [connections, setConnections] = useState<Connection[]>(getDefaultConnections());
  const [activeConnector, setActiveConnector] = useState<ConnectorType | null>(null);
  const [draggedFromPalette, setDraggedFromPalette] = useState(false);
  const [isConnecting, setIsConnecting] = useState(false);
  const [connectingFrom, setConnectingFrom] = useState<string | null>(null);
  const [currentMousePos, setCurrentMousePos] = useState({ x: 0, y: 0 });
  const [dragOffset, setDragOffset] = useState({ x: 0, y: 0 });
  const [configurationPanel, setConfigurationPanel] = useState<{
    isOpen: boolean;
    connector: CanvasConnector | null;
  }>({ isOpen: false, connector: null });
  const [dragStartPosition, setDragStartPosition] = useState<{ x: number; y: number } | null>(null);
  const [notes, setNotes] = useState<Array<{
    id: string;
    x: number;
    y: number;
    content: string;
  }>>(getDefaultNotes());
  const [isInitialized, setIsInitialized] = useState(false);
  
  const canvasRef = useRef<HTMLDivElement>(null);
  const scrollIntervalRef = useRef<NodeJS.Timeout | null>(null);

  // Auto-scroll functionality
  const startAutoScroll = useCallback((direction: 'left' | 'right' | 'up' | 'down') => {
    if (scrollIntervalRef.current) return;

    scrollIntervalRef.current = setInterval(() => {
      if (canvasRef.current) {
        const scrollAmount = 10;
        switch (direction) {
          case 'right':
            canvasRef.current.scrollLeft += scrollAmount;
            break;
          case 'left':
            canvasRef.current.scrollLeft -= scrollAmount;
            break;
          case 'down':
            canvasRef.current.scrollTop += scrollAmount;
            break;
          case 'up':
            canvasRef.current.scrollTop -= scrollAmount;
            break;
        }
      }
    }, 16); // ~60fps
  }, []);

  const stopAutoScroll = useCallback(() => {
    if (scrollIntervalRef.current) {
      clearInterval(scrollIntervalRef.current);
      scrollIntervalRef.current = null;
    }
  }, []);

  const handleMouseMove = useCallback((e: MouseEvent) => {
    // Track current mouse position for drop coordinates
    setCurrentMousePos({ x: e.clientX, y: e.clientY });

    // AUTO-SCROLL TEMPORARILY DISABLED
    // Uncomment the code below to re-enable auto-scroll functionality
    
    /*
    if (!canvasRef.current) return;

    const workspaceRect = canvasRef.current.getBoundingClientRect();
    const mouseX = e.clientX - workspaceRect.left;
    const mouseY = e.clientY - workspaceRect.top;
    const scrollZone = 80; // pixels from edge to trigger scroll

    // Horizontal scrolling
    if (mouseX < scrollZone && canvasRef.current.scrollLeft > 0) {
      startAutoScroll('left');
    } else if (mouseX > workspaceRect.width - scrollZone) {
      // Check if we can scroll right or need to expand
      if (canvasRef.current.scrollLeft < canvasRef.current.scrollWidth - canvasRef.current.clientWidth) {
        startAutoScroll('right');
      } else {
        // At the right edge, canvas will expand automatically via dynamic sizing
        stopAutoScroll();
      }
    } 
    // Vertical scrolling
    else if (mouseY < scrollZone && canvasRef.current.scrollTop > 0) {
      startAutoScroll('up');
    } else if (mouseY > workspaceRect.height - scrollZone) {
      // Check if we can scroll down or need to expand
      if (canvasRef.current.scrollTop < canvasRef.current.scrollHeight - canvasRef.current.clientHeight) {
        startAutoScroll('down');
      } else {
        // At the bottom edge, canvas will expand automatically via dynamic sizing
        stopAutoScroll();
      }
    } else {
      stopAutoScroll();
    }
    */
  }, []);

  const sensors = useSensors(
    useSensor(MouseSensor, {
      activationConstraint: {
        distance: 8,
      },
    }),
    useSensor(TouchSensor, {
      activationConstraint: {
        delay: 200,
        tolerance: 5,
      },
    })
  );

  const handleDragStart = useCallback((event: DragStartEvent) => {
    const { active } = event;
    const activeId = active.id as string;
    
    // Get current mouse position from the event or use stored position
    const activatorEvent = event.activatorEvent as MouseEvent;

    let offsetX = activatorEvent.offsetX;
    let offsetY = activatorEvent.offsetY;
    
    // Try to get more precise positioning from node reference if available
    if (active.data.current?.getNode) {
      const node = active.data.current.getNode();
      
      if (node) {
        const rect = node.getBoundingClientRect();
        const mouseX = activatorEvent.clientX;
        const mouseY = activatorEvent.clientY;
        
        // Calculate more precise offset
        const preciseOffsetX = mouseX - rect.left;
        const preciseOffsetY = mouseY - rect.top;
        
        // Use precise calculation if available
        offsetX = preciseOffsetX;
        offsetY = preciseOffsetY;
      }
    }
    // Store the offset to adjust drag overlay positioning
    setDragOffset({ x: -offsetX, y: -offsetY });
    
    // Check if dragging from palette
    const paletteConnector = connectorTypes.find(c => c.id === activeId);
    if (paletteConnector) {
      setActiveConnector(paletteConnector);
      setDraggedFromPalette(true);
      // AUTO-SCROLL TEMPORARILY DISABLED
      // Uncomment the line below to re-enable auto-scroll functionality
      // document.addEventListener('mousemove', handleMouseMove);
      return;
    }
    
    // Check if dragging from canvas (connector)
    const canvasConnector = canvasConnectors.find(c => c.id === activeId);
    if (canvasConnector) {
      setActiveConnector(canvasConnector);
      setDraggedFromPalette(false);
      setDragStartPosition({ x: canvasConnector.x, y: canvasConnector.y });
      // Start listening for mouse move to enable auto-scroll
      document.addEventListener('mousemove', handleMouseMove);
      return;
    }
    
    // Check if dragging a note
    const note = notes.find(n => n.id === activeId);
    if (note) {
      setDraggedFromPalette(false);
      setDragStartPosition({ x: note.x, y: note.y });
      // Start listening for mouse move to enable auto-scroll
      document.addEventListener('mousemove', handleMouseMove);
    }
  }, [canvasConnectors, notes, handleMouseMove, currentMousePos]); // eslint-disable-line react-hooks/exhaustive-deps

  const handleDragMove = useCallback((event: DragMoveEvent) => {
    const { active, delta } = event;
    const activeId = active.id as string;
    
    // Only update position for canvas items (not palette items)
    if (!draggedFromPalette && delta && dragStartPosition) {
      const canvasRect = document.getElementById('canvas')?.getBoundingClientRect();
      if (canvasRect) {
        // Allow movement beyond canvas bounds for expansion
        const newX = Math.max(10, dragStartPosition.x + delta.x);
        const newY = Math.max(10, dragStartPosition.y + delta.y);
        
        // Update connector position
        setCanvasConnectors(prev => prev.map(connector => {
          if (connector.id === activeId) {
            return { ...connector, x: newX, y: newY };
          }
          return connector;
        }));
        
        // Update note position
        setNotes(prev => prev.map(note => {
          if (note.id === activeId) {
            return { ...note, x: newX, y: newY };
          }
          return note;
        }));
      }
    }
  }, [draggedFromPalette, dragStartPosition, setNotes]);

  const handleDragEnd = useCallback((event: DragEndEvent) => {
    const { active, over } = event;
    if (!over) {
      setActiveConnector(null);
      setDraggedFromPalette(false);
      return;
    }

    const overId = over.id as string;
    const activeId = active.id as string;

    // If dropping on canvas from palette
    if (overId === 'canvas' && draggedFromPalette) {
      const connector = connectorTypes.find(c => c.id === activeId);
      if (connector) {
        const canvasRect = document.getElementById('canvas')?.getBoundingClientRect();

        if (canvasRect) {
          // Use the tracked mouse position for accurate drop coordinates
          // Account for canvas scroll offset
          const canvasElement = document.getElementById('canvas');
          const scrollLeft = canvasElement?.scrollLeft || 0;
          const scrollTop = canvasElement?.scrollTop || 0;
          
          const dropX = currentMousePos.x - canvasRect.left + scrollLeft;
          const dropY = currentMousePos.y - canvasRect.top + scrollTop;

          // Ensure the connector stays within reasonable bounds but allow expansion
          const newConnector: CanvasConnector = {
            ...connector,
            id: `${connector.id}-${Date.now()}`,
            x: Math.max(10, dropX),
            y: Math.max(10, dropY),
          };

          setCanvasConnectors(prev => [...prev, newConnector]);
        }
      }
    }
    
    // Position updates are now handled in handleDragMove for real-time movement

    setActiveConnector(null);
    setDraggedFromPalette(false);
    setDragOffset({ x: 0, y: 0 });
    setDragStartPosition(null);
    
    // AUTO-SCROLL TEMPORARILY DISABLED
    // Uncomment the lines below to re-enable auto-scroll functionality
    // document.removeEventListener('mousemove', handleMouseMove);
    // stopAutoScroll();
  }, [draggedFromPalette, handleMouseMove, stopAutoScroll, currentMousePos, canvasConnectors]); // eslint-disable-line react-hooks/exhaustive-deps

  const handleRemoveConnector = useCallback((id: string) => {
    setCanvasConnectors(prev => prev.filter(c => c.id !== id));
    // Also remove any connections involving this connector
    setConnections(prev => prev.filter(conn =>
      conn.fromId !== id && conn.toId !== id
    ));
    // canvasConnectors is not needed in deps as we only use the setter function
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  const handleClearCanvas = useCallback(() => {
    setCanvasConnectors([]);
    setConnections([]);
    setNotes([]);
    setIsConnecting(false);
    setConnectingFrom(null);
  }, []);

  const handleStartConnection = useCallback((fromId: string) => {
    setIsConnecting(true);
    setConnectingFrom(fromId);
  }, []);

  const handleFinishConnection = useCallback((toId: string) => {
    if (connectingFrom && connectingFrom !== toId) {
      const newConnection: Connection = {
        id: `${connectingFrom}-${toId}-${Date.now()}`,
        fromId: connectingFrom,
        toId: toId,
      };
      setConnections(prev => [...prev, newConnection]);
    }
    setIsConnecting(false);
    setConnectingFrom(null);
  }, [connectingFrom]);

  const handleCancelConnection = useCallback(() => {
    setIsConnecting(false);
    setConnectingFrom(null);
  }, []);

  const handleConfigureConnector = useCallback((connector: CanvasConnector) => {
    setConfigurationPanel({ isOpen: true, connector });
  }, []);

  const handleCloseConfigurationPanel = useCallback(() => {
    setConfigurationPanel({ isOpen: false, connector: null });
  }, []);

  const handleSaveConfiguration = useCallback((connectorId: string, config: any) => {
    // Here you would typically save the configuration to your backend or state
    // For now, we'll just log it
  }, []);

  const handleAutoArrange = useCallback(() => {
    const canvasRect = document.getElementById('canvas')?.getBoundingClientRect();
    if (!canvasRect) return;
    
    // First, arrange connectors considering connections for optimal layout
    const arrangedConnectors = canvasConnectors.length > 0 ? (() => {
      const connectorWidth = 140; // Base width for compact connectors
      const horizontalSpacing = connectorWidth + 80; // Space between connectors
      const verticalSpacing = 120; // Vertical space between rows
      const startX = 50;
      const startY = 50;
      
      // Build connection graph to understand relationships
      const connectionGraph = new Map();
      canvasConnectors.forEach(connector => {
        connectionGraph.set(connector.id, { 
          connector, 
          outgoing: [], 
          incoming: [],
          level: 0 
        });
      });
      
      // Populate connections
      connections.forEach(conn => {
        const fromNode = connectionGraph.get(conn.fromId);
        const toNode = connectionGraph.get(conn.toId);
        if (fromNode && toNode) {
          fromNode.outgoing.push(conn.toId);
          toNode.incoming.push(conn.fromId);
        }
      });
      
      // Calculate levels for hierarchical layout (left-to-right flow)
      const calculateLevels = () => {
        const visited = new Set();
        const levels = new Map();
        
        // Find root nodes (no incoming connections)
        const rootNodes = Array.from(connectionGraph.values())
          .filter(node => node.incoming.length === 0)
          .map(node => node.connector.id);
        
        if (rootNodes.length === 0 && canvasConnectors.length > 0) {
          // If no clear roots, start with first connector
          rootNodes.push(canvasConnectors[0].id);
        }
        
        // BFS to assign levels
        const queue = rootNodes.map(id => ({ id, level: 0 }));
        
        while (queue.length > 0) {
          const { id, level } = queue.shift()!;
          
          if (visited.has(id)) continue;
          visited.add(id);
          levels.set(id, level);
          
          const node = connectionGraph.get(id);
          if (node) {
            node.outgoing.forEach((targetId: string) => {
              if (!visited.has(targetId)) {
                queue.push({ id: targetId, level: level + 1 });
              }
            });
          }
        }
        
        // Handle disconnected components
        canvasConnectors.forEach(connector => {
          if (!levels.has(connector.id)) {
            levels.set(connector.id, 0);
          }
        });
        
        return levels;
      };
      
      const levels = calculateLevels();
      const maxLevel = Math.max(...Array.from(levels.values()));
      
      // Group connectors by level
      const levelGroups = new Map();
      for (let i = 0; i <= maxLevel; i++) {
        levelGroups.set(i, []);
      }
      
      canvasConnectors.forEach(connector => {
        const level = levels.get(connector.id) || 0;
        levelGroups.get(level).push(connector);
      });
      
      // Position connectors level by level
      const positioned: typeof canvasConnectors = [];
      let currentX = startX;
      
      for (let level = 0; level <= maxLevel; level++) {
        const connectorsInLevel = levelGroups.get(level) || [];
        if (connectorsInLevel.length === 0) continue;
        
        // Sort connectors in level to minimize connection crossings
        const sortedConnectors = connectorsInLevel.sort((a: any, b: any) => {
          const aConnections = connections.filter(c => c.fromId === a.id || c.toId === a.id).length;
          const bConnections = connections.filter(c => c.fromId === b.id || c.toId === b.id).length;
          return bConnections - aConnections; // More connected connectors first
        });
        
        // Position connectors vertically in this level
        const levelX = currentX; // Capture currentX for this level
        sortedConnectors.forEach((connector: any, index: number) => {
          positioned.push({
            ...connector,
            x: levelX,
            y: startY + index * verticalSpacing
          });
        });
        
        // Move to next level position
        currentX += horizontalSpacing;
      }
      
      return positioned;
    })() : canvasConnectors;
    
    // Update connectors first
    setCanvasConnectors(arrangedConnectors);
    
    // Small delay to ensure state update is processed before connection re-rendering
    setTimeout(() => {
      // Force a re-render by updating a dummy state if needed
      setIsInitialized(prev => !prev ? true : prev);
    }, 10);
    
    // Then arrange notes to fill empty spaces
    if (notes.length > 0) {
      const findEmptySpacesForNotes = () => {
        const noteWidth = 200;
        const noteHeight = 120;
        const margin = 40; // Increased margin to avoid dotted line overlap
        const gridSize = 50; // Increased grid size for better spacing
        
        // Get occupied areas from arranged connectors (including dotted line space)
        const occupiedAreas = arrangedConnectors.map(connector => ({
          x: connector.x - 40, // Extra space on left for potential incoming lines
          y: connector.y,
          width: 140 + 80, // Connector width + dotted line extension (right side)
          height: 80   // Collapsed height during auto-arrange
        }));
        
        // Check if a position overlaps with any occupied area (including already placed notes)
        const isPositionOccupied = (x: number, y: number, alreadyPlacedNotes: any[] = []) => {
          // Check against connectors
          const connectorOverlap = occupiedAreas.some(area => 
            x < area.x + area.width + margin &&
            x + noteWidth + margin > area.x &&
            y < area.y + area.height + margin &&
            y + noteHeight + margin > area.y
          );
          
          // Check against already placed notes in this arrangement
          const noteOverlap = alreadyPlacedNotes.some(note =>
            x < note.x + noteWidth + margin &&
            x + noteWidth + margin > note.x &&
            y < note.y + noteHeight + margin &&
            y + noteHeight + margin > note.y
          );
          
          return connectorOverlap || noteOverlap;
        };
        
        // Find available positions using comprehensive three-phase search
        const findPositionsForNotes = (notesToPlace: any[]) => {
          const positionedNotes = [];
          
          for (let i = 0; i < notesToPlace.length; i++) {
            const note = notesToPlace[i];
            let foundPosition = null;
            
            // Phase 1: Search within current canvas bounds thoroughly
            const currentMaxX = canvasRect.width - noteWidth - margin;
            const currentMaxY = canvasRect.height - noteHeight - margin;
            
            // Scan vertically first within existing canvas
            for (let x = margin; x <= currentMaxX && !foundPosition; x += gridSize) {
              for (let y = margin; y <= currentMaxY && !foundPosition; y += gridSize) {
                if (!isPositionOccupied(x, y, positionedNotes)) {
                  foundPosition = { x, y };
                }
              }
            }
            
            // Phase 2: If no space found within current bounds, try modest expansion
            if (!foundPosition) {
              const expandedMaxX = Math.max(800, canvasRect.width * 1.2) - noteWidth - margin;
              const expandedMaxY = Math.max(600, canvasRect.height * 1.2) - noteHeight - margin;
              
              for (let x = margin; x <= expandedMaxX && !foundPosition; x += gridSize) {
                for (let y = margin; y <= expandedMaxY && !foundPosition; y += gridSize) {
                  if (!isPositionOccupied(x, y, positionedNotes)) {
                    foundPosition = { x, y };
                  }
                }
              }
            }
            
            // Phase 3: Fallback - place below existing content
            if (!foundPosition) {
              const bottomMostY = Math.max(
                ...arrangedConnectors.map(c => c.y + 80),
                ...positionedNotes.map(n => n.y + noteHeight),
                0
              );
              foundPosition = { x: margin, y: bottomMostY + margin };
            }
            
            positionedNotes.push({
              ...note,
              x: foundPosition.x,
              y: foundPosition.y
            });
          }
          
          return positionedNotes;
        };
        
        return findPositionsForNotes(notes);
      };
      
      const repositionedNotes = findEmptySpacesForNotes();
      setNotes(repositionedNotes);
    }
    
    // Auto-arrange complete
  }, [canvasConnectors, connections, notes, setNotes]);

  const handleAddNote = useCallback(() => {
    const canvasRect = document.getElementById('canvas')?.getBoundingClientRect();
    if (!canvasRect) return;
    
    // Find the first available empty space in the canvas
    const findEmptySpace = () => {
      const noteWidth = 200;
      const noteHeight = 120;
      const margin = 20;
      const gridSize = 40; // Check positions every 40px
      
      // Get all occupied areas (connectors and existing notes)
      const occupiedAreas = [
        ...canvasConnectors.map(connector => ({
          x: connector.x,
          y: connector.y,
          width: 140,
          height: 80
        })),
        ...notes.map(note => ({
          x: note.x,
          y: note.y,
          width: noteWidth,
          height: noteHeight
        }))
      ];
      
      // Check if a position overlaps with any occupied area
      const isPositionOccupied = (x: number, y: number) => {
        return occupiedAreas.some(area => 
          x < area.x + area.width + margin &&
          x + noteWidth + margin > area.x &&
          y < area.y + area.height + margin &&
          y + noteHeight + margin > area.y
        );
      };
      
      // Comprehensive search for empty space within current canvas bounds first
      const currentMaxX = canvasRect.width - noteWidth - margin;
      const currentMaxY = canvasRect.height - noteHeight - margin;
      
      // Phase 1: Search within current canvas bounds thoroughly
      // Scan vertically first (x outer loop, y inner loop) within existing canvas
      for (let x = margin; x <= currentMaxX; x += gridSize) {
        for (let y = margin; y <= currentMaxY; y += gridSize) {
          if (!isPositionOccupied(x, y)) {
            return { x, y };
          }
        }
      }
      
      // Phase 2: If no space found within current bounds, try expanding canvas area
      const expandedMaxX = Math.max(800, canvasRect.width * 1.2) - noteWidth - margin; // 20% wider
      const expandedMaxY = Math.max(600, canvasRect.height * 1.2) - noteHeight - margin; // 20% taller
      
      // Search in expanded area, still prioritizing vertical expansion
      for (let x = margin; x <= expandedMaxX; x += gridSize) {
        for (let y = margin; y <= expandedMaxY; y += gridSize) {
          if (!isPositionOccupied(x, y)) {
            return { x, y };
          }
        }
      }
      
      // Phase 3: Fallback - place below existing content
      const bottomMostY = Math.max(
        ...canvasConnectors.map(c => c.y + 80),
        ...notes.map(n => n.y + noteHeight),
        0
      );
      
      return { x: margin, y: bottomMostY + margin };
    };
    
    const { x: noteX, y: noteY } = findEmptySpace();
    
    const newNote = {
      id: `note-${Date.now()}`,
      x: noteX,
      y: noteY,
      content: 'New note',
    };
    
    setNotes(prev => [...prev, newNote]);
  }, [canvasConnectors, notes]);

  const handleUpdateNote = useCallback((noteId: string, updates: { x?: number; y?: number; content?: string }) => {
    setNotes(prev => prev.map(note => 
      note.id === noteId ? { ...note, ...updates } : note
    ));
  }, []);

  const handleRemoveNote = useCallback((noteId: string) => {
    setNotes(prev => prev.filter(note => note.id !== noteId));
  }, []);

  // Auto-arrange on initial load
  useEffect(() => {
    if (!isInitialized && canvasConnectors.length > 0) {
      // Small delay to ensure DOM is ready
      const timer = setTimeout(() => {
        handleAutoArrange();
        setIsInitialized(true);
      }, 100);
      
      return () => clearTimeout(timer);
    }
  }, [canvasConnectors, isInitialized, handleAutoArrange]);

  // Cleanup auto-scroll on unmount
  useEffect(() => {
    return () => {
      if (scrollIntervalRef.current) {
        clearInterval(scrollIntervalRef.current);
      }
    };
  }, []);

  return (
    <div className="drag-drop-canvas">
      <div className="drag-drop-canvas__header">
        <div>
          <h3>Integrations</h3>
          <p>Drag connectors from the palette to the canvas to build your integration flow</p>
        </div>
        <div className="drag-drop-canvas__actions">
          <span className="drag-drop-canvas__count">
            {canvasConnectors.length} connector{canvasConnectors.length !== 1 ? 's' : ''}
          </span>

          <button 
            className="btn-secondary" 
            onClick={handleClearCanvas}
            disabled={canvasConnectors.length === 0}
          >
            Clear Canvas
          </button>
        </div>
      </div>

      <DndContext
        sensors={sensors}
        collisionDetection={pointerWithin}
        onDragStart={handleDragStart}
        onDragMove={handleDragMove}
        onDragEnd={handleDragEnd}
      >
        <div className="drag-drop-canvas__workspace" ref={canvasRef}>
          <ConnectorPalette />
          <Canvas 
            connectors={canvasConnectors} 
            connections={connections}
            notes={notes}
            onRemoveConnector={handleRemoveConnector}
            onStartConnection={handleStartConnection}
            onFinishConnection={handleFinishConnection}
            onCancelConnection={handleCancelConnection}
            onConfigureConnector={handleConfigureConnector}
            onAutoArrange={handleAutoArrange}
            onAddNote={handleAddNote}
            onUpdateNote={handleUpdateNote}
            onRemoveNote={handleRemoveNote}
            isConnecting={isConnecting}
            connectingFrom={connectingFrom}
            draggedFromPalette={draggedFromPalette}
            configurationPanel={configurationPanel}
            onCloseConfigurationPanel={handleCloseConfigurationPanel}
            onSaveConfiguration={handleSaveConfiguration}
          />
        </div>

        <DragOverlay
          dropAnimation={null}
          style={{
            marginLeft: `${-dragOffset.x}px`,
            marginTop: `${-dragOffset.y}px`
          }}
        >
          {activeConnector && draggedFromPalette ? (
            <div style={{ 
              zIndex: 9999, 
              position: 'relative', 
              pointerEvents: 'none'
            }}>
              <ConnectorItem
                connector={activeConnector}
                isDragging={true}
                showConnectionPoint={false}
                isCompact={true}
              />
            </div>
          ) : null}
        </DragOverlay>
      </DndContext>


    </div>
  );
};
