import React, { useState, useEffect } from 'react';
import { GameConfig } from '../UndercoverGame';
import { PlayerAssignment } from './AssignmentPhase';
import './GameplayPhase.scss';

interface GameplayPhaseProps {
  gameConfig: GameConfig;
  playerAssignments: PlayerAssignment[];
  onGameComplete: (winner: 'civilians' | 'undercovers') => void;
}

type GamePhase = 'discussion' | 'voting' | 'elimination' | 'mr-white-guess' | 'game-over';

interface TurnOrder {
  playerIndex: number;
  player: PlayerAssignment;
  turnNumber: number;
}

const getRoleIcon = (role: string): string => {
  switch (role) {
    case 'regular': return '';
    case 'undercover': return '';
    case 'mrwhite': return '';
    default: return '';
  }
};

const getRoleDisplay = (role: string): string => {
  switch (role) {
    case 'regular': return ' Civilian';
    case 'undercover': return ' Undercover';
    case 'mrwhite': return ' Mr. White';
    default: return ' Civilian';
  }
};

export const GameplayPhase: React.FC<GameplayPhaseProps> = ({
  gameConfig,
  playerAssignments: initialAssignments,
  onGameComplete
}) => {
  const [assignments, setAssignments] = useState<PlayerAssignment[]>(initialAssignments);
  const [currentRound, setCurrentRound] = useState(1);
  const [currentPhase, setCurrentPhase] = useState<GamePhase>('discussion');
  const [turnOrder, setTurnOrder] = useState<TurnOrder[]>([]);
  const [eliminatedPlayer, setEliminatedPlayer] = useState<PlayerAssignment | null>(null);
  const [mrWhiteGuess, setMrWhiteGuess] = useState('');
  const [showMrWhiteGuess, setShowMrWhiteGuess] = useState(false);
  const [winner, setWinner] = useState<'civilians' | 'undercovers' | null>(null);
  const [gameStarted, setGameStarted] = useState(false);

  // Initialize assignments with isAlive status
  useEffect(() => {
    const initialized = initialAssignments.map(player => ({
      ...player,
      isAlive: true
    }));
    setAssignments(initialized);
  }, [initialAssignments]);

  // Generate fair turn order for each round
  const generateTurnOrder = (alivePlayers: PlayerAssignment[], round: number): TurnOrder[] => {
    const mrWhiteIndex = alivePlayers.findIndex(p => p.role === 'mrwhite');
    const otherPlayers = alivePlayers.filter(p => p.role !== 'mrwhite');
    
    // Shuffle other players
    const shuffledOthers = [...otherPlayers].sort(() => Math.random() - 0.5);
    
    let finalOrder: PlayerAssignment[] = [];
    
    if (mrWhiteIndex === -1) {
      // Mr. White is dead, just shuffle all players
      finalOrder = [...alivePlayers].sort(() => Math.random() - 0.5);
    } else {
      const mrWhite = alivePlayers[mrWhiteIndex];
      
      if (round === 1) {
        // Round 1: Mr. White cannot be 1st or 2nd
        if (shuffledOthers.length >= 2) {
          const position = Math.floor(Math.random() * (shuffledOthers.length - 1)) + 2; // Position 3 onwards
          finalOrder = [
            ...shuffledOthers.slice(0, position),
            mrWhite,
            ...shuffledOthers.slice(position)
          ];
        } else {
          // Not enough players, put Mr. White last
          finalOrder = [...shuffledOthers, mrWhite];
        }
      } else if (round === 2) {
        // Round 2: Mr. White cannot be 1st
        if (shuffledOthers.length >= 1) {
          const position = Math.floor(Math.random() * shuffledOthers.length) + 1; // Position 2 onwards
          finalOrder = [
            ...shuffledOthers.slice(0, position),
            mrWhite,
            ...shuffledOthers.slice(position)
          ];
        } else {
          // Not enough players, put Mr. White last
          finalOrder = [...shuffledOthers, mrWhite];
        }
      } else {
        // Round 3+: No restrictions
        const position = Math.floor(Math.random() * (shuffledOthers.length + 1));
        finalOrder = [
          ...shuffledOthers.slice(0, position),
          mrWhite,
          ...shuffledOthers.slice(position)
        ];
      }
    }

    return finalOrder.map((player, index) => ({
      playerIndex: assignments.findIndex(p => p.name === player.name),
      player,
      turnNumber: index + 1
    }));
  };

  // Check win conditions (only after game has started)
  useEffect(() => {
    // Don't check win conditions on initial load
    if (!gameStarted) return;

    const alivePlayers = assignments.filter(p => p.isAlive);
    const aliveUndercovers = alivePlayers.filter(p => p.role === 'undercover');
    const aliveCivilians = alivePlayers.filter(p => p.role === 'regular');
    const aliveMrWhite = alivePlayers.filter(p => p.role === 'mrwhite');

    // Civilians win if no undercovers and no Mr. White left
    if (aliveUndercovers.length === 0 && aliveMrWhite.length === 0) {
      setWinner('civilians');
      setCurrentPhase('game-over');
      onGameComplete('civilians');
      return;
    }

    // Undercovers win if they outnumber civilians (excluding Mr. White)
    // Note: Equal numbers means game continues, undercovers need majority to win
    if (aliveUndercovers.length > aliveCivilians.length && aliveUndercovers.length > 0) {
      setWinner('undercovers');
      setCurrentPhase('game-over');
      onGameComplete('undercovers');
      return;
    }

    // Special case: If only 2 players left and 1 is undercover, undercovers win
    // This handles the final 1v1 scenario where undercovers have achieved parity
    if (alivePlayers.length === 2 && aliveUndercovers.length === 1) {
      setWinner('undercovers');
      setCurrentPhase('game-over');
      onGameComplete('undercovers');
    }
  }, [assignments, gameStarted, onGameComplete]);

  // Generate turn order when round starts
  useEffect(() => {
    if (currentPhase === 'discussion') {
      const alivePlayers = assignments.filter(p => p.isAlive);
      const newTurnOrder = generateTurnOrder(alivePlayers, currentRound);
      setTurnOrder(newTurnOrder);
    }
  }, [currentPhase, currentRound, assignments]);

  const startVoting = () => {
    setCurrentPhase('voting');
    setGameStarted(true); // Mark game as started when first voting begins
  };

  const eliminatePlayer = (playerIndex: number) => {
    const eliminated = assignments[playerIndex];
    setEliminatedPlayer(eliminated);
    
    // Update assignments
    const updatedAssignments = [...assignments];
    updatedAssignments[playerIndex].isAlive = false;
    setAssignments(updatedAssignments);

    // Check if Mr. White was eliminated
    if (eliminated.role === 'mrwhite') {
      setCurrentPhase('mr-white-guess');
    } else {
      setCurrentPhase('elimination');
    }
  };

  const handleMrWhiteGuess = (guess: string) => {
    setMrWhiteGuess(guess);
    setShowMrWhiteGuess(true);
    
    // Check if guess is correct
    const correctWord = assignments.find(p => p.role === 'regular')?.word;
    const isCorrect = guess.toLowerCase().trim() === correctWord?.toLowerCase().trim();
    
    if (isCorrect) {
      // Mr. White wins!
      setWinner('undercovers'); // Mr. White is on undercover team
      setCurrentPhase('game-over');
      onGameComplete('undercovers');
    } else {
      // Mr. White loses, continue game
      setCurrentPhase('elimination');
    }
  };

  const continueAfterElimination = () => {
    setCurrentPhase('discussion');
    setEliminatedPlayer(null);
    setShowMrWhiteGuess(false);
    setMrWhiteGuess('');
    setCurrentRound(currentRound + 1);
  };

  const getAlivePlayers = () => assignments.filter(p => p.isAlive);

  if (currentPhase === 'game-over' && winner) {
    return (
      <div className="gameplay-phase gameplay-phase--game-over">
        <h2>Game Over!</h2>
        <div className="game-results">
          <h3>{winner === 'civilians' ? ' Civilians Win!' : ' Undercovers Win!'}</h3>
          <div className="final-roles">
            <h4>Final Roles:</h4>
            {assignments.map((player) => (
              <div key={player.name} className="player-role">
                <span className="player-name">{player.name}</span>
                <span className={`player-role-badge player-role-badge--${player.role}`}>
                  {getRoleDisplay(player.role)}
                </span>
                {player.word && <span className="player-word">({player.word})</span>}
              </div>
            ))}
          </div>
          <button className="play-again-btn" onClick={() => window.location.reload()}>
            Play Again
          </button>
        </div>
      </div>
    );
  }

  if (currentPhase === 'mr-white-guess' && eliminatedPlayer) {
    return (
      <div className="gameplay-phase gameplay-phase--mr-white-guess">
        <h2>Mr. White Eliminated!</h2>
        <div className="elimination-result">
          <h3>{eliminatedPlayer.name}</h3>
          <div className="player-role-reveal">
            <span className="role-name"> Mr. White</span>
          </div>

          <div className="mr-white-guess">
            {!showMrWhiteGuess ? (
              <div className="guess-input">
                <p> Mr. White, guess the civilian word to win the game!</p>
                <input
                  type="text"
                  placeholder="Enter your guess..."
                  value={mrWhiteGuess}
                  onChange={(e) => setMrWhiteGuess(e.target.value)}
                  className="guess-input-field"
                  onKeyDown={(e) => e.key === 'Enter' && mrWhiteGuess.trim() && handleMrWhiteGuess(mrWhiteGuess)}
                />
                <button
                  className="guess-submit-btn"
                  onClick={() => handleMrWhiteGuess(mrWhiteGuess)}
                  disabled={!mrWhiteGuess.trim()}
                >
                  Submit Guess
                </button>
              </div>
            ) : (
              <div className="guess-result">
                <p>Your guess: <strong>{mrWhiteGuess}</strong></p>
                <p>The correct word was: <strong>{assignments.find(p => p.role === 'regular')?.word}</strong></p>
                <p className={mrWhiteGuess.toLowerCase().trim() === assignments.find(p => p.role === 'regular')?.word?.toLowerCase().trim() ? 'guess-correct' : 'guess-incorrect'}>
                  {mrWhiteGuess.toLowerCase().trim() === assignments.find(p => p.role === 'regular')?.word?.toLowerCase().trim() ? 
                    ' Correct! Mr. White wins the game!' : 
                    ' Wrong! Mr. White is eliminated.'}
                </p>
                {mrWhiteGuess.toLowerCase().trim() !== assignments.find(p => p.role === 'regular')?.word?.toLowerCase().trim() && (
                  <button className="continue-btn" onClick={continueAfterElimination}>
                    Continue Game
                  </button>
                )}
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }

  if (currentPhase === 'elimination' && eliminatedPlayer) {
    return (
      <div className="gameplay-phase gameplay-phase--elimination">
        <h2>Player Eliminated!</h2>
        <div className="elimination-result">
          <h3>{eliminatedPlayer.name}</h3>
          <div className="player-role-reveal">
            <span className="role-name">
              {getRoleDisplay(eliminatedPlayer.role)}
            </span>
          </div>
        </div>

        <button className="continue-btn" onClick={continueAfterElimination}>
          Continue to Round {currentRound + 1}
        </button>
      </div>
    );
  }

  if (currentPhase === 'voting') {
    const alivePlayers = getAlivePlayers();

    return (
      <div className="gameplay-phase gameplay-phase--voting">
        <h2>Round {currentRound} - Elimination Phase</h2>
        <p>Choose a player to eliminate. Click on a player to vote them out immediately.</p>

        <div className="voting-grid">
          {alivePlayers.map((player, index) => {
            const originalIndex = assignments.findIndex(p => p.name === player.name);
            
            return (
              <div key={originalIndex} className="player-vote-card">
                <h3>{player.name}</h3>
                <button
                  className="eliminate-btn"
                  onClick={() => eliminatePlayer(originalIndex)}
                >
                  Vote Out {player.name}
                </button>
              </div>
            );
          })}
        </div>
      </div>
    );
  }

  // Discussion phase
  return (
    <div className="gameplay-phase">
      <h2>Round {currentRound} - Discussion Phase</h2>

      <div className="turn-order">
        <h3>Speaking Order</h3>
        <div className="turn-order-list">
          {turnOrder.map((turn, index) => (
            <div key={turn.playerIndex} className="turn-item">
              <span className="turn-number">{turn.turnNumber}.</span>
              <span className="player-name">{turn.player.name}</span>
            </div>
          ))}
        </div>
      </div>

      <div className="game-status">
        <div className="alive-players">
          <h3>Alive Players ({getAlivePlayers().length})</h3>
          <div className="players-list">
            {getAlivePlayers().map((player) => (
              <div key={player.name} className="player-item">
                <span className="player-name">{player.name}</span>
                <span className="player-status">Alive</span>
              </div>
            ))}
          </div>
        </div>

        <div className="game-info">
          <p>Each player speaks in the order shown above about their word.</p>
          <p>Try to figure out who the Undercovers and Mr. White are!</p>
          <p><strong>Remember:</strong> Mr. White doesn't know the word, and Undercovers have a different word.</p>
        </div>
      </div>

      <div className="game-actions">
        <button className="start-voting-btn" onClick={startVoting}>
          Start Elimination Phase
        </button>
      </div>
    </div>
  );
};